////
/// Hallway-cards
/// @group patterns
////

.hallway-cards__wrapper {
    grid-column: 1/-1;
    background-color: $color-hallway-bg;
    padding-bottom: $measure--small4;

    .hallway-cards__heading {
        color: $color-purewhite;
        text-align: center;
    }

    @include media('>tablet') {
        .hallway-cards {
            display: grid;
            grid-template-columns: 1fr 1fr 1fr;
        }

        &[data-card-count='2'],
        &[data-card-count='4'] {
            .hallway-cards {
                grid-template-columns: 1fr 1fr;
                .hallway-cards__card {
                    .hallway-cards__card__img {
                        .figure--image {
                            img {
                                max-height: $card-half-max-image-height;
                            }
                        }
                    }
                }                
            }
        }
    }

    @include media('<=tablet') {
        .hallway-cards__card--linked:not(:first-of-type) {
            .hallway-cards__card {
                margin-top: $measure--small2;
            }
        }
    }

    .hallway-cards {
        @include default-padding();
        padding-top: $measure--basic;
        grid-template-columns: 1fr 1fr 1fr;

        .hallway-cards__card {
            position: relative;

            .hallway-cards__card__img {
                overflow: hidden;

                .figure--image {
                    position: relative;
                    overflow: hidden;
                    margin-top: 0;

                    &:after {
                        background: $color-nearblack;
                        width: 100%;
                        height: 100%;
                        position: absolute;
                        content: '';
                        left: 0;
                        opacity: 0.5;
                        top: 0;
                        transition: 0.2s ease-in-out;
                    }

                    img {
                        display: block;
                        width: 100%;
                        transition: transform 0.55s ease-in-out;
                        max-height: $card-third-max-image-height;
                        object-fit: cover;
                    }
                }
            }

            .hallway-cards__card__details__wrap {
                position: absolute;
                top: 0;
                width: 100%;
                height: 100%;
                align-items: center;
                display: flex;
            }

            .hallway-cards__card__details {
                color: $color-purewhite;
                @include text-shadow( $shadowIntensity: 0.8);
                text-align: center;
                width: 100%;

                .hallway-cards__card__details__title {
                    margin-top: 0;
                    display: inline;
                    padding-left: $measure--quarter;
                    padding-right: $measure--quarter;
                    @include epsilontype;
                }



                .hallway-cards__card__details__text {
                    @include font-family(heading);
                    @include smalltype;
                    @include heading-font-features;
                }
            }

            &:hover {

                .hallway-cards__card__details__seperator {
                    width: $measure--medium9;
                    margin-top: $measure--small2;
                    margin-bottom: $measure--small2;
                }

                & .hallway-cards__card__img {

                    & img {
                        transform: scale(1.02);
                    }

                    .figure--image:after {
                        opacity: 0.7;
                    }
                }
            }
        }

        .hallway-cards__card--linked {
            text-decoration: none;
            border: none;

            &:focus, 
            &:active {
                outline: none;
                
                & .hallway-cards__card__details__title {
                    @include outline-properties($color-purewhite);
                }

                & .hallway-cards__card__img {

                    .figure--image:after {
                        background: $color-hallway-focus-bg;
                        opacity: 1;
                    }
                }
            }
        }
    }
}

.hallway-cards__card__details__seperator {
    background-color: $color-hallway-separator;
    content: '';
    display: block;
    height: $border-width-default;
    margin-left: auto;
    margin-right: auto;
    width: $measure--large2;
    margin-top: $measure--threequarters;
    margin-bottom: $measure--threequarters;
    transition: 0.2s ease-in-out;
}

///
/// bayes and city school colours
///

@each $theme, $colors in $clp-themes {
        $bg-color-lead: nth($colors, 1);
        $txt-color-lead: nth($colors, 2);
        $bg-color-accent: nth($colors, 3);

    .city-lite--#{$theme} {
        .hallway-cards__wrapper {
            background-color: $bg-color-lead;

            .hallway-cards__heading {
                color: $txt-color-lead;
            }
        }
        .hallway-cards__card__details__seperator {
            background-color: $bg-color-accent;
        }
    }
}

body[class*="city-lite--c"] {  // City Hallway seperator alway grey
    .hallway-cards__card__details__seperator {
        background-color: $color-hallway-separator;
    }
}
