.card {
    list-style-type: none;
    max-width: unset;

    .card__anchor {
        @include anchor-reset();
        @include card-anchor-colors();

        display: flex;
        flex-direction: column;
        overflow: hidden;
        position: relative;
        transition: background-color $transition-duration ease-in;

        @include media('reducemotion') {
            transition: none;
        }

        &,
        &:focus,
        &:active,
        &:visited {
            border-width: $border-width-hairline;
            border-style: $border-style-solid;
        }

        &:hover {
            @include media('hover') {
                border-style: $border-style-solid;
                border-width: $border-width-hairline;
            }
        }

        &:after {
            @include font-family('icon');

            align-items: center;
            bottom: 0;
            content: '\f178';
            display: flex;
            font-weight: $font-weight-light;
            height: $measure--small4;
            justify-content: center;
            position: absolute;
            right: 0;
            text-align: center;
            transition: background-color $transition-duration ease-in;
            width: $measure--small4;

            @include media('reducemotion') {
                transition: none;
            }
        }

        .card__heading {
            border-bottom-style: $border-style-solid;
            border-bottom-width: $border-width-default;
        }
    }

    .card__details {
        .card__details__text {
            padding-bottom: $measure--medium3;
            padding-left: $measure--basic;
            padding-right: $measure--basic;
            padding-top: $measure--basic;
        }

        .card__heading {
            @include h5style();
            display: inline;
            max-width: unset;
        }

        .card__type {
            @include alphatype();

            margin-top: 0;
            max-width: unset;
        }

        .card__description {
            @include content-style();
        }

        .card__tag {
            @include smalltype();

            max-width: unset;

            & + .card__tag {
                margin-top: 0;
            }

            .icon {
                margin-right: $measure--half;
            }
        }
    }
}
