////
/// Global Header
/// @group patterns
////
.global-header {
    background-color: $color-bg;

    @include media('print') {
        display: none;
    }

   @include media('>=small') {
        @include standard-box();
        @include x-column-grid(24);
    }
}

.global-header__content {
    display: flex;
    align-items: center;
    background-color: $color-purewhite;
    @include box-shadow();
    grid-column: 1/-1;

    @include media('<=mobile') {
        flex-direction: column;
    }

    @include media('>mobile') {
        flex-direction: row;
        flex-wrap: nowrap;
        justify-content: space-between;
    }

    @include media('>small') {
        @include standard-grid();
        @include default-padding();

        grid-template-rows: repeat(3, auto);
    }

    @include media('>desktop') {
        grid-row-gap: $measure--basic;
        grid-template-rows: repeat(2, auto);
    }
}

.global-header__utility {
    @include media('<=mobile') {
        width: 100%;
        margin-bottom: $measure--basic;
        display: flex;
    }
    
    @include media('>mobile') {
        padding-right: $measure--basic;
    }

    @include media('>small') {
        grid-column-start: 18;
        grid-column-end: 25;
        grid-row-end: span 2;
        grid-row-start: 1;

        display: flex;
        align-items: center;
        justify-content: end;
    }

    .global-header__utility__e {
        display: block;
        min-height: 3rem;
        width: 100%;
        padding: $measure--half;

        @include media('<=mobile') {
            @include default-padding();
            justify-self: stretch;
        }

        @include media('>small') {
            padding: 0;
            text-align: end;
        }

        a[class^="cta"] {
            text-align: center;
            @include media('<=mobile') {
                display: block;
            }
        }
    }
}

.header-logo {
    @include media('<=mobile') {
        margin-top: $measure--basic;
    }
    
    @include media('<=small') {
        padding-bottom: $measure--half;
        padding-left: $measure--half;
        padding-right: $measure--half;
        padding-top: $measure--half;
        width: $measure--huge5;
    }

    @include media('>small') {
        grid-column-end: span 3;
        grid-column-start: 1;
        grid-row-end: span 2;
        grid-row-start: 1;
    }

    .logo {
        width: 100%;
        display: inline-block;

        @include media('<=small') {
            height: $measure--medium7;
        }

        @include media('>small') {
            margin-bottom: $measure--small2;
            margin-top: $measure--small2;
        }
    }
}
