.carousel-gallery-default {
    background-color: $color-gallery-bg;
    margin-bottom: $measure--small2;
    h2,
    h3,
    h4 {
        color: $color-purewhite;
    }

    .swiper-slide__text {
        background-color: initial;
        color: $color-purewhite;

        a {
            @include anchor-inline(
                $color-gray4,
                $color-gray4,
                $color-nearblack,
                $color-outline--dark
            );
        }
    }

    .swiper-slider-arrow {
        @include control-button($color-purewhite);

        &:focus {
            background-color: transparent;
        }
        &:hover,
        &:active {
            color: $color-gallery-bg;
            background-color: $color-purewhite;
        }

        &:hover,
        &:focus:focus,
        &:active:active {
            outline-color: $color-purewhite;
        }
    }

    .swiper-slide__image-wrapper {    
        background-color: $color-gallery-shadow;
    }

    .galleria-thumbnail {
        & input {
            @include anchor-default-outline($color-outline--dark);

            &:active {
                outline-color: $color-gallery-bg;
            }
        }

        &.active input,
        &.active input:focus {
                outline-color: $color-gallery-bg;
        }
    }
}

.carousel-gallery {
    overflow: hidden;
    position: relative;

    h2,
    h3,
    h4 {
        margin-top: 0;
        padding-bottom: $measure--half;
    }

    .swiper-slide {
        margin-right: 0;
        margin-top: 0;
    }

    .swiper-slide__text {
        @include smalltype;
        display: block;
        top: 0;
        width: 100%;
        margin-right: unset;
        margin-left: unset;
        padding: $measure--basic 0;
    }

    .swiper-buttons-wrap {
        bottom: $measure--half;
        right: $measure--half;

        @include media('<between') {
            position: initial;
            padding: $measure--half;
            justify-content: flex-end;
        }
    }

    .swiper-slide__image-wrapper {
        height: 22rem;

        @include media('<desktop') {
            height: 16rem;
        }

        @include media('<tablet') {
            height: $measure--huge5;
        }

        picture {
            display: flex;
        }

        & img {
            object-fit: contain;
        }
    }
}

.galleria-thumbnails-wrap {
    width: calc(100% - 7rem);
    overflow: hidden;

    @include media('<between') {
        width: calc(100%);
    }
}

.no-js {
    .galleria-thumbnails-wrap {
        display: none;
    }
}

.js {
    .carousel-gallery-default {
        padding: $measure--basic;
        margin-top: $measure--basic;
        display: grid; // FF fix
    }

    .swiper-wrapper {
        align-items: initial;
    }

    .galleria-thumbnails-list {
        --thumbnails: 1;
        display: flex;
        list-style: none;
        margin: 0;
        width: calc(var(--thumbnails) * 25%);
        max-width: none;
        align-items: baseline;
        transition: transform 0.5s;

        @include media('<between') {
            width: calc(var(--thumbnails) * 33%);
        }

        .galleria-thumbnail {
            max-width: calc(100% / var(--thumbnails));
            margin-top: 0;
            margin-right: $measure--half;

            &:first-child {
                margin-left: $border-width-default;
            }

            & input {
                opacity: 0.7;
                max-width: 100%;
                height: auto;
                border: 0;
                max-height: $measure--medium3;
                margin-top: $measure--quarter;
                transition: opacity 0.6s;

                &:focus {
                    opacity: 1;
                    outline-offset: 0;
                }

                &:active {
                    outline-style: solid;
                    outline-width: $border-width-default;
                    outline-offset: 0;
                }

                &:hover {
                    opacity: 1;
                }
            }

            &.active input,
            &.active input:focus {
                opacity: 1;
                outline-style: solid;
                outline-width: $border-width-default;
                outline-offset: 0;
            }

            &.blurSlider {
                opacity: 0.7;
            }

            .galleria-thumbnail__current-thumbnail {
                @include screenreader-only;
            }
        }
    }
}

@each $theme, $colors in $clp-themes {
    $bgColor-pri: nth($colors, 1);
    $textColor-pri: nth($colors, 2);
    $bgColor-sec: nth($colors, 3);
    $textColor-sec: nth($colors, 4);
    $anchorColor-pri: nth($colors, 7);
    $anchorColor-sec: nth($colors, 8);

    .city-lite--#{$theme} {
        
        .carousel-gallery--bayes-theme-primary {
            background-color: $bgColor-pri;
        
            h2,
            h3,
            h4 {
                color: $textColor-pri;
            }

            .swiper-slide__image-wrapper {
                background-color: $color-gray14;
            }
        
            .swiper-slide__text {
                background-color: initial;
                color: $textColor-pri;
        
                a {
                    @include anchor-inline(
                        $textColor-pri,
                        $textColor-pri,
                        $anchorColor-pri,
                        $textColor-pri
                    );
                }
            }
        
            .swiper-slider-arrow {
                @include control-button($textColor-pri);
        
                &:focus {
                    background-color: transparent;
                }
                &:hover,
                &:active {
                    color: $bgColor-pri;
                    background-color: $textColor-pri;
                }
        
                &:hover,
                &:focus:focus,
                &:active:active {
                    outline-color: $textColor-pri;
                }
            }
        
            .galleria-thumbnail {
                & input {
                    @include anchor-default-outline($color-outline--dark);
        
                    &:active {
                        outline-color: $textColor-pri;
                    }
                }
        
                &.active input,
                &.active input:focus {
                        outline-color: $textColor-pri;
                }
            }
        }

        .carousel-gallery--bayes-theme-secondary {
            background-color: $bgColor-sec;
        
            h2,
            h3,
            h4 {
                color: $textColor-sec;
            }

            .swiper-slide__image-wrapper {
                background-color: $color-gray14;
            }
        
            .swiper-slide__text {
                background-color: initial;
                color: $textColor-sec;
        
                a {
                    @include anchor-inline(
                        $textColor-sec,
                        $textColor-sec,
                        $anchorColor-sec,
                        $textColor-sec
                    );
                }
            }
        
            .swiper-slider-arrow {
                @include control-button($textColor-sec);
        
                &:focus {
                    background-color: transparent;
                }
                &:hover,
                &:active {
                    color: $bgColor-sec;
                    background-color: $textColor-sec;
                }
        
                &:hover,
                &:focus:focus,
                &:active:active {
                    outline-color: $textColor-sec;
                }
            }
        
            .galleria-thumbnail {
                & input {
                    @include anchor-default-outline($color-outline);
        
                    &:active {
                        outline-color: $textColor-sec;
                    }
                }
        
                &.active input,
                &.active input:focus {
                        outline-color: $textColor-sec;
                }
            }
        }
        
    }

}