////
/// Key selling points (KSPs) block
/// @group patterns
////

@each $theme, $colors in $clp-themes { 
    $bgColor: nth($colors, 3);
    $textColor: nth($colors, 4);
    $cta: nth($colors, 5);
    $invert: nth($colors, 6);

    .city-lite--#{$theme} {
        .ksp-block[data-theme='bayes-theme']  { // Bayes KSP classname
            background-color: $bgColor;
            color: $textColor;
            .wrapper--ksp__item__icon {
                background-color: $textColor;
            }
            .ksp__item__icon {
                filter: $invert;
            }
            .cta--ksp {
                @extend .cta--#{$cta};
            }
        }

        // Exeption for MBA not following normal pattern as other themes
        @if $theme == 'b-mba' {
            .ksp-block[data-theme='bayes-theme']  {
                background: $color-mba-lead;
                color: $color-purewhite;
        

                .wrapper--ksp__item__icon {
                    background-color: $bgColor;
                }

                .cta--ksp {
                    @extend .cta--#{$cta};
                }
            }
        }

        // Exeption for all city themes not following normal pattern as other themes, to ALL look the same
        @if $theme == 'c-science' or $theme == 'c-law' or $theme == 'c-health' or $theme == 'c-creativity' or $theme == 'c-policy' {
            .ksp-block[data-theme='bayes-theme']  {
                background-color: $color-gray10;
                color: $color-purewhite;

                .ksp__item__icon {
                    filter: invert(100%);
                }

                .cta--ksp {
                    @extend .cta--#{'inverse--bg'};
                }
            }
        }
    }
    
    .js {
        .city-lite--#{$theme} {
            .ksp-block[data-theme='bayes-theme'] {
                .slider[data-style='dots']:not([data-small='true']):not([data-count='0']),
                .slider[data-style='dots'][data-small='true']:not([data-count='0']) {
                    & + .slider__controls {
                        button {
                            &[aria-expanded='true'] {
                                background-color: $textColor;
                            }
                            background-color: transparent;
                            border: $border-width-hairline
                                $border-style-solid
                                $textColor;
                            &:active,
                            &:focus {
                                border-color: $textColor;
                                outline-color: $textColor;
                            }
                        }
                    }
                    .slide {
                        &:focus {
                            outline-color: $textColor;
                        }
                    }
                }
            }
        }
    }
}

// default city styles for when no theme is present
[class="city-lite city-lite"] {
    .ksp-block[data-theme='bayes-theme']  {
        background-color: $color-gray10;
        color: $color-purewhite;
      
        .wrapper--ksp__item__icon {
            background-color: $color-purewhite;
        }
    
        .ksp__item__icon {
            filter: invert(100%);
        }
    
        .cta--ksp {
            @extend .cta--#{'inverse--bg'};
        }
    }
}

.ksp-block {
    &[data-theme='light'] {
        .wrapper--ksp__item__icon {
            background-color: $color-ksp-dark-bg;
        }
    }

    &[data-theme='dark'] {
        background-color: $color-ksp-dark-bg;
        color: $color-purewhite;

        .wrapper--ksp__item__icon {
            background-color: $color-ksp-dark-bg;
        }

        .ksp__item__icon {
            filter: invert(0);
        }

        a[class*="cta"] {
            @extend .cta--inverse--bg;
            border-bottom: $border-width-default $border-style-solid $color-purewhite;

            &:visited {
               border-bottom: $border-width-default $border-style-solid $color-purewhite;
            }

            &:active,
            &:focus {
                outline-color: $color-purewhite;
                border-bottom: $border-width-default $border-style-solid $color-purewhite;
            }
        }
    }

    .wrapper--ksp__item__icon {
        border-radius: 100%;
        display: inline-flex;
    }

    @include media('<tablet') {
        .ksp-block__heading,
        .ksp-block__introduction,
        .cta-block {
            padding: 0 $measure--small2;
        }
    }

    @include media('>=tablet') {
        .ksp__items {
            display: flex;
        }
    }

    @include media('>=small') {
        @include standard-grid();
        .ksp-block__heading,
        .ksp-block__introduction,
        .cta-block {
            grid-column: 7/-7;
        }
    }

    // Generic
    background: $color-gray1;
    padding: $measure--basic 0;

    .ksp-block__heading,
    .ksp-block__introduction,
    .cta-block {
        margin-left: auto;
        margin-right: auto;
        text-align: center;
        .cta-block__cta {
            margin-bottom: $measure--basic;
        }
    }

    .ksp__items {
        grid-column: 1/-1;
        margin: $measure--basic 0;
        max-width: none;
        text-align: center;

        .ksp__item {
            list-style-type: none;
            padding: 0 $measure--basic;
            flex: 1;
        }

        .ksp__item__description {
            margin: $measure--threequarters auto 0;
        }

        .ksp__item__icon {
            width: $measure--medium7;
            height: $measure--medium7;
        }
    }
}

.js {
    .ksp-block {
        .slider {
            &[data-cardsperrow='1']:not([data-small='true']),
            &[data-cardsperrow='2']:not([data-small='true']),
            &[data-cardsperrow='3']:not([data-small='true']) {
                .slide {
                    // Centre slide content; overrides margin-right in slider component
                    margin: 0 calc(#{$measure--small1} / 2);
                }
            }
        }
    }
}

///
/// City school KSP colours
/// 

@each $theme, $colors in $clp-themes {
    $ksp-background: nth($colors, 12);
    $school-color-text: nth($colors, 2);
    $cta: nth($colors, 5);
    $color-filter: nth($colors, 11);
    $icon-bg-color: nth($colors, 10);

    .city-lite--#{$theme} {
        .ksp-block {
            &[data-theme='light'] {
                .wrapper--ksp__item__icon {
                    background-color: $icon-bg-color;
                }

                .ksp__item__icon {
                    filter: $color-filter;
                }
            }

            &[data-theme='dark'] {
                background-color: $ksp-background;
                color: $school-color-text;

                .wrapper--ksp__item__icon {
                    background-color: $icon-bg-color;
                }

                .ksp__item__icon {
                    filter: $color-filter;
                }

                a[class*="cta"] {
                    @extend .cta--#{$cta};
                   
                    &:active,
                    &:focus {
                        outline-color: $school-color-text;
                    }
                }
            }
        }
    }
}
