@mixin pathway-card(
    $card-text-color: $color-nearblack,
    $card-text-hover-color: $color-nearblack,
    $card-background-color: transparent,
    $card-background-hover-color: $color-gray1,
    $card-heading-color: $color-nearblack,
    $section-title-color: $color-nearblack,
    $border-color: $color-gray1,
    $outline-color: $color-nearblack
) {
    @include anchor-reset();
    @include background-color-and-transition(
        $card-background-color,
        $card-background-hover-color
    );

    border: $border-width-hairline $border-style-solid $border-color;
    color: $card-text-color;
    display: block;

    .card--pathway__content__heading {
        color: $section-title-color;
    }

    .card--pathway__content__heading__text {
        border-bottom-color: $section-title-color;
        border-bottom-style: $border-style-solid;
        border-bottom-width: $border-width-default;
    }

    p {
        color: $card-text-color;
    }

    .card--pathway__content__img {
        transition: transform $transition-duration ease-in-out;
        overflow: hidden;
    }

    @include media('reducemotion') {
        transition: none;
    }

    &:hover {
        @include media('hover') {
            .card--pathway__content__img {
                transform: scale(1.02);
            }

            .card--pathway__content__heading,
            p {
                color: $card-text-hover-color;
            }

            .card--pathway__content__heading__text {
                border-bottom-color: $card-text-hover-color;
            }
        }

        border-bottom-color: $border-color;
        border-bottom-style: $border-style-solid;
        border-bottom-width: $border-width-hairline;
    }

    &:active,
    &:focus {
        @include outline-properties($outline-color);

        .card--pathway__content__heading,
        p {
            color: $card-text-hover-color;
        }

        .card--pathway__content__heading__text {
            border-bottom-color: $card-text-hover-color;
        }
    }

    &:visited {
        .card--pathway__content__heading {
            color: $section-title-color;
        }

        p {
            color: $card-text-color;
        }

        .card--pathway__content__heading__text {
            border-bottom-color: $section-title-color;
        }
    }
}
