////
/// Bayes campaign banner
////

///
/// basic banner (this is city) override rules
///
body[class*="city-lite--b"] {

    .banner {
        background-color: $color-purewhite;
        color: $color-nearblack;
        border-bottom: 1px solid $color-gallery;
    }

    .wrapper--txt-ribbon {
        position:relative;
        
        @include media('>tablet') {
            grid-column: 14/-2;
            grid-row: 1/-1;
        }
    }

    .banner__img {
        @include media('>tablet') {
            grid-column: 1 / 13;
        }
    }

    .banner__txt {
        @include media('<=tablet') {
            margin: $measure--small4 0 0 0;
        }
    }
}

.banner--bayes-highlight {
    .banner__txt__padding {
        padding: 0 $measure--half;

        h1,
        p,
        span {
            box-decoration-break: clone;
            line-height: $measure--medium3;
            display: inline;
            
            @include media('>=tablet'){
                line-height: $measure--medium5;
            }
        }
    }
}

///
/// Bayes program banner colours
/// 

$bayes-banner-themes: (
    b: (
        $color-corporate-lead, // lead BG => on corporate CTA it is opposite
        $color-purewhite, // lead txt
    ),
    b-ug: (
        $color-ug-lead,
        $color-nearblack,
    ),
    b-msc: (  
        $color-msc-lead,
        $color-purewhite,
    ),
    b-phd: (  
        $color-phd-lead,
        $color-purewhite,
    ),
    b-mba: (  
        $color-mba-accent,
        $color-gray10,
    ),
    b-emba: (  
        $color-emba-lead,
        $color-purewhite,
    ),
    b-demba: (  
        $color-demba-lead,
        $color-nearblack,
    ),
    b-omba: (  //online mba/msc
        $color-omba-lead,
        $color-nearblack,
    ),
    b-exec: (  
        $color-exec-lead,
        $color-purewhite,
    ),
    b-cce: (  
        $color-cce-lead,
        $color-purewhite,
    )
);

@each $theme, $colors in $bayes-banner-themes {
    $bg-color-lead: nth($colors, 1);
    $txt-color-lead: nth($colors, 2);

    .city-lite--#{$theme} {
        .banner--bayes-highlight {
            .banner__txt__padding {
                h1,
                p,
                span {
                    color: $txt-color-lead;
                    background-color: $bg-color-lead;
                    box-shadow: $measure--half 0 0 $bg-color-lead, -$measure--half 0 0 $bg-color-lead;
                }
            }
        }
    }
}

///
/// new element(s)
///
body[class*="city-lite--b"] {
    .banner__separator {
        display: none;
        
        @include media('>tablet') {
            display: block;
            grid-column: 11/14;
            grid-row: 1/-1;
            height: 100%;
            border-top: $measure--medium7 solid $color-purewhite;
            border-right: $measure--medium7 solid $color-purewhite;
            border-bottom: $measure--medium7 solid $color-purewhite;
        }
    }

    .banner__separator__mobile {
        display: block;
        width: $measure--medium11; // 5rem
        height: $measure--medium11;
        background-color: $color-purewhite;
        position: absolute;
        right:0;
        top: -$measure--medium3;

        @include media('>tablet') {
            display: none;
        }
    }
}
