.cta-block {
    list-style: none;
    margin: 0;

    .cta-block__cta {
        display: inline-block;

        &:not(:last-of-type) {
            margin-right: $measure--basic;
        }
    }
}

.cta-block--centre {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-left: auto;
    margin-right: auto;
}
