////
/// Figure
/// @group patterns
////

figure a {
    @include anchor-img();
}

figure figcaption,
.figcaption-like {
    @include smalltype();
    color: $color-text--caption;
    font-weight: $font-weight-book;
    line-height: $measure--small1;
    margin-top: $measure--quarter;

    a {
        @include anchor-discreet();
    }
}

.figcaption__credit {
    display: block;
}

figure.figure--svg img {
    @include media('<=mobile') {
        padding-bottom: 100vw;
    }
}
