@mixin anchor-inline(
    $color: $color-inline-link,
    $visited: $color-inline-link--visited,
    $hover: $color-text--dark,
    $outline: $color-outline
) {
    @include anchor-reset();
    @include anchor-hover-background($color);
    @include anchor-default-outline($outline);

    border-bottom-color: $color;
    border-bottom-style: $border-style-solid;
    border-bottom-width: $border-width-default;
    color: $color;

    &:visited {
        border-bottom-color: $visited;
        border-bottom-style: $border-style-solid;
        border-bottom-width: $border-width-default;
        color: $visited;
    }

    &:focus {
        border-bottom-color: $color;
        border-bottom-style: $border-style-solid;
        border-bottom-width: $border-width-default;
        color: $color;
    }

    &:hover {
        @include media('hover') {
            border-bottom-color: $color;
            border-bottom-style: $border-style-solid;
            border-bottom-width: $border-width-default;
            color: $hover;
        }
    }

    &:active {
        background-color: $color;
        border-bottom-color: $color;
        border-bottom-style: $border-style-solid;
        border-bottom-width: $border-width-default;
        color: $hover;
    }
}

@mixin anchor-inline--subelement(
    $color: $color-inline-link,
    $visited: $color-inline-link--visited,
    $hover: $color-text--dark,
    $outline: $color-outline,
    $class: 'inline-text'
) {
    @include anchor-reset();
    @include anchor-default-outline($outline);

    .#{$class} {
        @include anchor-reset();
        @include anchor-hover-background($color);

        border-bottom-color: $color;
        border-bottom-style: $border-style-solid;
        border-bottom-width: $border-width-default;
        color: $color;
    }

    &:visited {
        .#{$class} {
            border-bottom-color: $visited;
            border-bottom-style: $border-style-solid;
            border-bottom-width: $border-width-default;
            color: $visited;
        }
    }

    &:focus {
        .#{$class} {
            border-bottom-color: $color;
            border-bottom-style: $border-style-solid;
            border-bottom-width: $border-width-default;
            color: $color;
        }
    }

    &:hover {
        @include media('hover') {
            .#{$class} {
                border-bottom-color: $color;
                border-bottom-style: $border-style-solid;
                border-bottom-width: $border-width-default;
                color: $hover;
            }
        }
    }

    &:active {
        .#{$class} {
            background-color: $color;
            border-bottom-color: $color;
            border-bottom-style: $border-style-solid;
            border-bottom-width: $border-width-default;
            color: $hover;
        }
    }
}
