///
/// Bayes program and City school colours
/// 
$clp-control-buttons: (
    b: ( //Bayes
        $color-purewhite, // lead txt - white outline
        $color-nearblack, // opposit of lead text
    ),
    b-ug: (
        $color-nearblack, // black outline
        $color-purewhite 
    ),
    b-msc: (  
        $color-purewhite,
        $color-nearblack,
    ),
    b-phd: (  
        $color-purewhite,
        $color-nearblack,
    ),
    b-mba: (  
        $color-purewhite,
        $color-nearblack,
    ),
    b-emba: (  
        $color-purewhite,
        $color-nearblack,
    ),
    b-demba: (  
        $color-nearblack, // black outline
        $color-purewhite 
    ),
    b-omba: (  //online mba/msc
        $color-nearblack, //black outline
        $color-purewhite 
    ),
    b-exec: (  
        $color-purewhite,
        $color-nearblack,
    ),
    b-cce: (  
        $color-purewhite,
        $color-nearblack,
    ),
    c-health: (  // City school colours start
        $color-nearblack, //black outline
        $color-purewhite 
    ),
    c-science: (  
        $color-nearblack, //black outline
        $color-purewhite  
    ),
    c-policy: (  
        $color-purewhite,
        $color-nearblack,
    ),
    c-creativity: (  
        $color-purewhite,
        $color-nearblack, 
    ),
    c-law: (  
        $color-purewhite,
        $color-nearblack,
    )
);


@each $theme, $colors in $clp-control-buttons {
    $txt-color-lead: nth($colors, 1);
    $txt-color-opposite: nth($colors, 2);

    .city-lite--#{$theme} {
        .promo {
            .slider[data-style="arrows"] {
                + .slider__controls {
                    .slider__indicator {
                        .slider__indicator__divider, 
                        .slider__indicator__current, 
                        .slider__indicator__total {
                            color: $txt-color-lead;
                        }
        
                        .slider__indicator__divider, 
                        .slider__indicator__current {
                            opacity: 0.8;
                        }
                    }
        
                        .slider__controls__next, 
                        .slider__controls__prev {
        
                        &:focus {
                            background-color: transparent;
                            color: $txt-color-lead;
                        }
        
                        &:focus:not(:disabled),
                        &:active:not(:disabled) {
                            @include outline-properties($txt-color-lead);
                        }
        
                        &:hover:not(:disabled) {
                            @include media('hover') {
                                background-color: $txt-color-lead;
                                color: $txt-color-opposite;
                            }
                        }
                    }
                }
            } 
        }
    }
}