@mixin screenreader-only {
    border: 0;
    clip: rect(0, 0, 0, 0);
    height: 1px;
    margin: -1px;
    overflow: hidden;
    padding: 0;
    position: absolute;
    width: 1px;
}

//Alias for sr-only as no longer part of fontawesome mixins
@mixin sr-only {
    @include screenreader-only;
}

@mixin sr-only-focusable-within {
    &:active,
    &:focus,
    &:focus-within {
        clip: auto;
        height: auto;
        margin: 0;
        overflow: visible;
        position: static;
        width: auto;
    }
}

.sr-only-focusable-within {
    @include sr-only-focusable-within();
}

.sr-only {
    @include screenreader-only;
}

@mixin box-shadow-slim($opacity: 0.15) {
    box-shadow: 0 0.125rem $measure--quarter 0 rgba(0, 0, 0, $opacity);
}

@mixin box-shadow {
    box-shadow: 0 $measure--quarter $measure--basic 0 rgba(0, 0, 0, 0.15);
}

@mixin box-shadow-darker {
    box-shadow: 0 $measure--quarter $measure--basic 0 rgba(0, 0, 0, 0.35);
}

@mixin veil() {
    background-color: rgba(30, 30, 30, 0.5);
    bottom: 0;
    display: none;
    left: 0;
    position: absolute;
    right: 0;
    top: 0;
    z-index: z-index('veil');

    &[data-on='true'] {
        display: block;
    }
}

//remove transform: scale(1.02) pattern change on pathway cards
//at the moment this mixin only use on pathway card
@mixin background-color-and-transition(
    $initial-background-color: transparent,
    $background-color: $color-gray1
) {
    background-color: $initial-background-color;
    transition: background-color $transition-duration ease-in,
        transform $transition-duration ease-in-out;

    @include media('reducemotion') {
        transition: none;
    }

    &:hover {
        @include media('hover') {
            background-color: $background-color;
        }
    }

    &:active {
        background-color: $background-color;
    }

    &:focus {
        background-color: $background-color;
    }

    @include media('<=small') {
        background-color: $background-color;
    }
}

@mixin thumbnail-gradient-scale($scale: 1.04, $background-opacity: 0.1) {
    transition: transform $transition-duration ease-in-out;

    @include media('reducemotion') {
        transition: none;
    }

    &:hover {
        @include media('hover') {
            background-image: none;

            img {
                transform: scale($scale);
            }

            .thumbnail__image-container {
                &:after {
                    background-color: rgba(0, 0, 0, $background-opacity);
                }
            }
        }
    }

    .thumbnail__image-container {
        overflow: hidden;
        position: relative;
        transition: transform $transition-duration ease-in;
        width: $measure--huge3;
        height: $measure--huge1;

        @include media('reducemotion') {
            transition: none;
        }

        &:after {
            background-color: transparent;
            content: '';
            display: block;
            height: 100%;
            left: 0;
            position: absolute;
            top: 0;
            transition: background-color $transition-duration ease-in;
            width: 100%;
        }

        img {
            transition: transform $transition-duration ease-in;
            object-fit: cover;
            height: 100%;
            width: 100%;

            @include media('reducemotion') {
                transition: none;
            }
        }
    }
}

@mixin card-shared-properties(
    $title-color: $color-nearblack,
    $paragraph-color: $color-gray10,
    $arrow-link-color: $color-nearblack,
    $arrow-link-hover-color: $color-brand-primary
) {
    position: relative;
    padding: $measure--basic;

    &:visited {
        .card__title {
            color: $color-inline-link--visited;
        }
    }

    &:hover {
        @include media('hover') {
            background-image: none;

            @include media('>small') {
                background: transparent;
            }

            .card__arrow-link {
                background-color: $arrow-link-hover-color;
            }
        }
    }

    &:active {
        @include media('>small') {
            background: transparent;
        }

        .card__arrow-link {
            background-color: $arrow-link-hover-color;
        }
    }

    &:focus {
        @include media('>small') {
            background: transparent;
        }
    }

    .card__title {
        @include h3style();
        @include betatype();
        color: $title-color;
        display: inline;
    }

    .card__arrow-link {
        position: absolute;
        bottom: 0;
        right: 0;
        padding: $measure--threequarters;
        background-color: $arrow-link-color;
        color: $color-purewhite;
        transition: background-color $transition-duration ease-in;
    }

    p {
        @include alphatype();
        color: $paragraph-color;
    }
}

/**
    Used where card has two divs, one most likely
    being a thumbnail.
**/
@mixin card-with-thumbnail {
    display: block;

    @include media('>=tablet') {
        display: flex;
    }

    > :nth-child(2) {
        margin-top: $measure--basic;

        @include media('>=tablet') {
            margin-top: 0;
        }
    }
}

/*
    USAGE:

    $pseudo: both / before / after
    $background-color: any hex color
    $skew: a degree value

    Background color must match background color of element mixin is applied to
*/

@mixin angled-edge(
    $pseudo: both,
    $background-color: $color-gray5,
    $skew: 4deg
) {
    position: relative;

    @if ($pseudo == before or $pseudo == both) {
        &:before {
            position: absolute;
            left: 0;
            right: 0;
            z-index: -1;
            content: '';
            display: none;
            background-color: $background-color;
            height: $measure--medium11;
            transform: skewY($skew);
        }
    }

    @if ($pseudo == after or $pseudo == both) {
        &:after {
            position: absolute;
            left: 0;
            right: 0;
            z-index: -1;
            content: '';
            display: none;
            background-color: $background-color;
            height: $measure--medium11;
            transform: skewY($skew);
        }
    }

    &:before {
        top: -$measure--medium3;

        @if ($pseudo == before or $pseudo == both) {
            display: block;
        }
    }

    &:after {
        bottom: -$measure--medium3;

        @if ($pseudo == after or $pseudo == both) {
            display: block;
        }
    }
}

@mixin gradient-border($color1: $color-brand-primary, $color2: $color-pink60) {
    border: $measure--threequarters solid;
    border-image-source: linear-gradient(45deg, $color1, $color2);
    border-image-slice: 1;
}

////
// this button mixin is used on: testimonial slider, image-gallery
////
@mixin control-button($color1: $color-brand-primary) {
    color: $color1;
    border: $border-width-hairline solid $color1;
    padding: $measure--threequarters;
    font-weight: $font-weight-normal;

    &:hover {
        @include media('hover') {
            color: $color-purewhite;
            background: $color1;
        }
    }

    &:focus,
    &:active {
        color: $color-purewhite;
        background: $color1;
        @include anchor-default-outline($color-outline);
    }

    &:disabled {
        color: $color-gray7;
        background: $color-purewhite;
        border: none;
        opacity: 0.7;
    }
}

@mixin heading-centered() {
    text-align: center;

    &:after {
        content: '';
        width: $measure--medium1;
        height: $measure--quarter;
        display: block;
        background: $color-brand-primary;
        margin: $measure--quarter auto $measure--basic auto;
    }
}

.heading-centered {
    @include heading-centered();
}

@mixin svg-path-animation {
    transition: stroke-dashoffset 1.5s;

    &.animate--svg-path--jsinit {
        display: none;
    }

    &.animate--svg-path--jsrun {
        display: block;
    }
}

@mixin corner-gradient-background(
    $main-color: $color-red60,
    $corner-color: $color-red70
) {
    background-color: $main-color;

    background: linear-gradient(55deg, $main-color 70%, $corner-color 0%);

    @include media('>tiny') {
        background: linear-gradient(55deg, $main-color 75%, $corner-color 0%);
    }

    @include media('>=mobile') {
        background: linear-gradient(55deg, $main-color 75%, $corner-color 0%);
    }

    @include media('>=tablet') {
        background: linear-gradient(55deg, $main-color 75%, $corner-color 0%);
    }

    @include media('>=between') {
        background: linear-gradient(45deg, $main-color 75%, $corner-color 0%);
    }

    @include media('>=small') {
        background: linear-gradient(55deg, $main-color 75%, $corner-color 0%);
    }

    @include media('>=desktop') {
        background: linear-gradient(45deg, $main-color 75%, $corner-color 0%);
    }

    @include media('>=large') {
        background: linear-gradient(45deg, $main-color 75%, $corner-color 0%);
    }
}

@mixin contact-card(
    $color: $color-nearblack,
    $icon-color: $color-brand-primary,
    $border-color: $color-nearblack
) {
    h2,
    h3 {
        color: $color;
    }

    .card--contact {
        color: $color;
        border-top: $border-width-hairline $border-style-solid
            rgba($border-color, 0.2);
    }

    .icon {
        color: $icon-color;
    }
}

@mixin dropdown-filter-select(
    $border-color: $color-pink90,
    $color: $color-nearblack,
    $background-color: transparent
) {
    background-color: $background-color;
    color: $color;
    border: $border-width-default $border-style-solid $border-color;
}

//table reset

@mixin table-reset {
    display: initial;
    margin-top: 0;
    min-width: 66%;
    max-width: 100%;
    overflow-x: initial;
    border-collapse: initial;
    font-weight: initial;

    tr {
        th,
        td {
            border: none;
            vertical-align: initial;
            &:first-child {
                text-align: initial;
            }
            &:not(:first-child) {
                text-align: initial;
            }
        }
    }

    th,
    strong {
        font-weight: initial;
    }

    thead {
        tr {
            color: initial;
            &:first-child {
                th {
                    background: initial;
                }
            }
            .heading__secondary {
                background: initial;
            }
        }
        td,
        th {
            padding: 0;
            vertical-align: initial;
        }
    }

    tbody {
        color: initial;
        tr {
            &:hover {
                background-color: initial;
            }
        }
        th,
        td {
            padding: 0;
            vertical-align: initial;
        }
    }
}

// used on social media footer icons
@mixin anchor-icon(
    $color: $color-text,
    $visited: $color-text,
    $hover: $color-brand-primary,
    $outline: $color-text
) {
    @include anchor-reset-properties();

    border-color: $color;
    color: $color;
    display: inline-block;

    &:hover {
        @include media('hover') {
            border-color: $hover;

            .icon {
                color: $hover;
            }
        }
    }

    &:active,
    &:focus {
        @include outline-properties($outline);
    }
}

@mixin card-anchor-colors(
    $background: transparent,
    $color: $color-nearblack,
    $background-hover: $color-gray1,
    $color-hover: $color-nearblack,
    $color-outline: $color-nearblack,
    $icon: $color-brand-primary,
    $arrow-background: $color-nearblack,
    $arrow-color: $color-purewhite,
    $arrow-background-hover: $color-brand-primary,
    $arrow-color-hover: $color-purewhite,
    $border: $color-gray1
) {
    background-color: $background;
    color: $color;

    &:focus,
    &:active {
        @include outline-properties($color-outline);
    }

    &,
    &:focus,
    &:active,
    &:visited {
        border-color: $border;
        color: $color;
    }

    &:hover {
        @include media('hover') {
            background-color: $background-hover;
            border-color: $border;
            color: $color;

            &:after {
                background-color: $arrow-background-hover;
                color: $arrow-color-hover;
            }
        }
    }

    &:after {
        background-color: $arrow-background;
        color: $arrow-color;
    }

    @include media('<=between') {
        background-color: $background-hover;

        &:after {
            background-color: $arrow-background-hover;
        }
    }

    .card__type {
        color: $icon;
    }

    .card__tag {
        .icon {
            color: $icon;
        }
    }
}
