.events-cards__wrapper {
    grid-column: 1/-1;
    @include standard-grid();
    background-color: $color-gray1;
    padding-bottom: $measure--small2;

    @include media('<between') {
        @include default-padding();
    }
}

.events-cards__column:first-child {
    grid-column: 2/12;
}

.events-cards {
    margin-left: 0;
    margin-top: $measure--small2;
    max-width: unset;
}

.events-cards__column {
    grid-column: 12/-2;
}

@include media('<between') {
    .events-cards__column:first-child ,.events-cards__column {
        grid-column: 1/-1;
    }
}