// styles for youtube video collage content template 

.embedded-video--collage__wrapper {
    grid-column: 1/-1;
    padding-bottom: $measure--half;

    @include media('>small'){
        grid-column: 2/-2;
    }

    @include media('<=small'){
            .slider[data-style="arrows"] + .slider__controls {
                margin-right: $measure--basic;
            }
        } 

    .embedded-video--collage__heading {
        text-align: center;
        @include media('<=small'){
            @include default-padding();
        }
    }

    .embedded-video--collage__container {  
        margin-top: $measure--basic;
        max-width: 100%;

        @include media('<=small'){
            @include default-padding();
            padding-top: $measure--half;
        }  
        
        display: grid;
        grid-column-gap: $measure--small2;
        grid-row-gap: $measure--small2;

        &--2-columns {
            grid-template-columns: 1fr 1fr;

            @include media('<=tablet') {
                grid-template-columns: 1fr;
            }
        }

        &--3-columns {
            grid-template-columns: 1fr 1fr 1fr;

            @include media('<=tablet') {
                grid-template-columns: 1fr;
            }
        }

        .embedded-video {
            padding-top: 0;
            padding-bottom: 0;

            .youtube-video {
                margin-top: 0;
            }
        }
    }
}
