main > .campaign {
    @include media('>=small') {
        .wrapper--pathway,
        .ksp--pathway {
            grid-column: 2/-2;
        }
    }

    @include media('<small') {
        .wrapper--pathway {
            padding: 0 $measure--small2;
            grid-column: 1/-1;
        }
        .pathway,
        .banner {
            grid-column: 1/-1;
        }
    }

    // Generic
    .ksp-block,
    .wrapper--ksp-block {
        grid-column: 1/-1;
    }
}

// Bayes specific MBA highlighted section headings
@mixin mba-highted-heading {
    color: $color-nearblack;
    background-color: $color-mba-accent;
    box-shadow: $measure--half 0 0 $color-mba-accent, -$measure--half 0 0 $color-mba-accent;
    display: inline-block;
}

.city-lite--b-mba {
    .pathway > h3, .pathway > h2, .embedded-video--collage__heading {
        @include mba-highted-heading;
    }
}
