.card--socialmedia {
    .card__anchor {
        background-color: $color-purewhite;

        &,
        &:focus,
        &:active,
        &:visited {
            border: none;
        }

        &:hover {
            @include media('hover') {
                border: none;

                .card--socialmedia__label--source {
                    background-color: $color-brand-primary;
                    color: $color-purewhite;
                }
            }
        }

        .card--socialmedia__label--source {
            align-items: center;
            background-color: $color-purewhite;
            border-color: $color-brand-primary;
            border-radius: 50%;
            border-style: solid;
            border-width: $border-width-default;
            color: $color-nearblack;
            display: flex;
            height: $measure--small4;
            justify-content: center;
            margin-bottom: $measure--half;
            transition: background-color $transition-duration ease-in,
                color $transition-duration ease-in;
            width: $measure--small4;

            @include media('reducemotion') {
                transition: none;
            }
        }

        .card__heading {
            @include sr-only();
        }

        .card--socialmedia__content {
            margin-bottom: $measure--small3;
            @include smalltype();
            color: $color-gray7;
        }

        .card--socialmedia__url {
            color: $color-brand-primary;
        }

        .card--socialmedia__label--socialmedia {
            @include smalltype();

            color: $color-gray7;
            font-style: italic;
            margin-left: $measure--basic;
            margin-top: $measure--basic;
        }

        .card--socialmedia__label--socialmedia + .card__thumbnail,
        .card--socialmedia__label--socialmedia + .card__thumbnail__wrapper {
            margin-top: $content-element-margin;
        }

        .card__type {
            margin-top: $content-element-margin;
        }

        &:after {
            content: unset;
            display: none;
        }

        .card--socialmedia__footer {
            @include smalltype();

            .card--socialmedia__screenname {
                color: $color-gray7;   
            }

            .card--socialmedia__time {
                color: $color-gray7;
                font-style: italic;
            }
        }
    }

    &.card--socialmedia--instagram {
        .card__thumbnail__wrapper {
            position: relative;
        }

        .card--socialmedia__label--source {
            position: absolute;
            top: $measure--basic;
            left: $measure--basic;
        }

        .card__thumbnail {
            display: block;
        }
    }
}

.js {
    .social-cards__wrapper {
        @include media('>tablet') {
            .related-content[data-style='arrows']:not(.slider) {
                grid-template-columns: repeat(auto-fill, minmax(21%, 1fr));
                max-width: initial;
            }
        }
    }
}

.social-cards__wrapper {
    grid-column: 1/-1;
    padding-left: $measure--small4;
    padding-right: $measure--small4;

    h2 {
        text-align: center;
    }
}
