// Desktop-specific layout
main {
    @include media('>=small') {
        .embedded-video {
            display: flex;
            flex-direction: row;
            flex-wrap: nowrap;

            .youtube-video {
                width: 100%;
            }
        }

        .embedded-video--with-heading {
            .embedded-video-details {
                width: calc(35% - #{$measure--small4});
                margin-right: $measure--small4;
            }

            .youtube-video {
                width: 65%;
            }
        }

        .youtube-video {
            margin: 0;
        }
    }
}

// Generic styles
.embedded-video {
    padding-bottom: $measure--small2;
    padding-top: $measure--small2;

    @include media('>between') {
        padding-bottom: $measure--small4;
        padding-top: $measure--small4;
    }

    &.embedded-video--autoplay {
        padding: 0;
        .embedded-video--autoplay__controller-btn {
            background-color: $color-purewhite;
            position: absolute;
            bottom: 0;
            right: 0;
            margin: $border-width-default;
            width: $measure--medium2;
            height: $measure--medium2;
            &:focus {
                outline-color: $color-nearblack;
                outline-offset: -$border-width-thick;
                outline-style: $border-style-dotted;
                outline-width: $border-width-default;
            }
            &:hover {
                background-color: $color-gray2;
            }
            &[data-status='play'] {
                .embedded-video--autoplay__controller-btn__icon {
                    &:after {
                        content: '\f144';
                    }
                }
            }
            &[data-status='pause'] {
                .embedded-video--autoplay__controller-btn__icon {
                    &:after {
                        content: '\f28b';
                    }
                }
            }
            .embedded-video--autoplay__controller-btn__icon {
                color: $color-nearblack;
                font-weight: $font-weight-light;
                font-family: 'Font Awesome 6 Pro';
                @include zetatype;
            }
        }
    }

    .embedded-video-details {
        display: flex;
        flex-direction: column;
        justify-content: center;
    }

    .embedded-video-details__heading {
        margin-top: 0;
        width: 100%;

        &:after {
            background: $color-pink80;
            content: '';
            display: block;
            height: $measure--quarter;
            margin-bottom: $measure--small4;
            margin-top: $measure--small4;
            width: $measure--large2;

            @include media('<small') {
                margin-bottom: $measure--small1;
                margin-top: $measure--small1;
            }
        }
    }

    .embedded-video-details__description {
        width: 100%;
    }
}

.youtube-video__wrapper {
    height: 0;
    padding-bottom: 56.25%;
    position: relative;
    width: 100%;

    iframe {
        height: 100%;
        left: 0;
        margin: 0;
        position: absolute;
        top: 0;
        width: 100%;
        border: none;
    }
}

.youtube-video__caption-element--text,
.youtube-video__caption-element--time {
    display: block;
}

.youtube-video__caption-element--time span {
    font-weight: $font-weight-extrablack;
}
