main {
    .yt-cta {
        @include media('>=small') {
            .youtube-video {
                margin-top: $measure--threequarters;
            }
        }

        @include media('>=tablet') {
            display: flex;
            grid-column: 2/-2;
            .yt-cta__heading {
                text-align: center;
            }
            .yt-cta__text, .yt-cta__video {
                flex: 1;
            }
            .yt-cta__text {
                margin-right: $measure--small2;
            }
        }
    
        @include media('<tablet') {
            grid-column: 1/-1;
            @include default-padding;
            .yt-cta__video {
                margin-top: $measure--small2; 
            }
        }
    
        // Generic
        margin: $measure--basic 0 $measure--small4 0;
        flex-wrap: wrap;
        .yt-cta__heading {
            flex-basis: 100%;
            margin-bottom: $measure--threequarters;
        }
        .yt-cta__text {
            .cta-block {
                margin: 0 0 $measure--small4;
            }
        }
    }
}

// Theme layout spacing
.yt-cta.yt-cta--colour-theme { 
    grid-column: 1/-1;
    padding-left: $measure--small4;
    padding-right: $measure--small4;
    padding-bottom: $measure--small4;
    
    .yt-cta__heading {
        margin-bottom: $measure--small2;
        margin-top: $measure--small2;
    }

    .yt-cta__video {
        position: relative;
        overflow: hidden;
        z-index: z-index('default');

        &:after {
            content: "";
            position: absolute;
            margin-right: $measure--small4;
            width: 100%;
            height: 100%;
            top: 0;
            right: 0;
            z-index: z-index('below');
        } 
        
        @include media('<tablet') {
            margin-top: 0;

            .youtube-video {
                margin-left: $measure--small4;
            }
        }
    }

    .youtube-video {
        margin-top: 0;
        
        figcaption {
            padding-right: $measure--small4;
            padding-bottom: $measure--threequarters;
        }
    }

    .yt-cta__text {
        padding: $measure--small2;
        margin-right: 0;
        margin-top: $measure--small4;

        @include media('<tablet') {
            margin-right: $measure--small4;
        }
    }
}

// City theme colour not in $clp-themes, as it would influence other patterns and this is pattern specific 
[class="city-lite city-lite--b-corporate"] {
    .yt-cta.yt-cta--colour-theme {
        background-color: $color-brand-secondary; 

        .yt-cta__heading {
            color: $color-purewhite;
        }

        .yt-cta__video {
            &:after {
                background-color: $color-purewhite;
            }
        }

        .yt-cta__text {
            background-color: $color-purewhite;

            &:focus {
                outline-color: $color-purewhite;
            }
        }
    }
}

// Bayes colours
@each $theme, $colors in $clp-themes {
    $bg-color-lead: nth($colors, 1);
    $txt-color-lead: nth($colors, 2);
    .city-lite--#{$theme} {
        .yt-cta.yt-cta--colour-theme { 
            background-color: $bg-color-lead;
            
            .yt-cta__heading {
                color: $txt-color-lead;
            }

            .yt-cta__video {
                &:after {
                    background-color: $color-testimonial-text-default;
                }
            }
    
            .yt-cta__text {
                background-color: $color-testimonial-text-default;
    
                &:focus {
                    outline-color: $txt-color-lead;
                }
            }
        }
    }       
}
