// @mixin cta--basic(
// $color: $color-purewhite,
// $background: $color-cta--t1, //$color-red50
// $hover-color: $color-purewhite,
// $hover-background: $color-cta-hover--t1, //$color-red70
// $outline: $color-purewhite,
// $color-border: $color-cta--t1,
// $color-border-hover: $color-cta-hover--t1, 
// $corners: square
// )

/// default / theme-1 CTA on bright background
.cta,
.cta--basic {
    @include cta--basic(
        $color-purewhite,
        $color-cta--t1,
        $color-purewhite,
        darken( $color-cta--t1, 6%),
        $color-nearblack,
        $color-cta--t1,
        darken( $color-cta--t1, 6%),
        round
    )
}

.cta--outline {
    @include cta--basic(
        $color-purewhite, // $color
        $color-cta--t1,   // $background
        $color-cta--t1,   // $hover-color
        $color-purewhite, // $hover-background
        $color-nearblack, // $outline
        $color-cta--t1,   // $color-border
        $color-cta--t1,   // $color-border-hover
        round             // corners
    )
}

/// default / theme-1 CTA on dark background
.cta--bg {
    @include cta--basic(
        $color-purewhite,
        $color-cta--t1,
        $color-purewhite,
        $color-cta-hover--t1,
        $color-purewhite,
        $color-cta--t1,
        $color-cta-hover--t1,
        round
    )
}

// theme-2 CTA on bright background
.cta--t2 {
    @include cta--basic(
        $color-purewhite,
        $color-cta--t2,
        $color-purewhite,
        $color-cta-hover--t2,
        $color-nearblack,
        $color-cta--t2,
        $color-cta-hover--t2,
        round
    )
}

// inverse CTA on bright background
.cta--inverse {
    @include cta--basic(
        $color-nearblack,
        transparent,
        $color-purewhite,
        $color-nearblack,
        $color-nearblack,
        $color-nearblack,
        $color-nearblack,
        round
    )
}

// inverse CTA on dark background
.cta--inverse--bg {
    @include cta--basic(
        $color-purewhite,
        transparent,
        $color-nearblack,
        $color-purewhite,
        $color-purewhite, 
        $color-purewhite, 
        $color-purewhite, 
        round
    )
}

.cta--duotone {
    @include cta--basic(
        $color-nearblack,
        $color-purewhite,
        $color-purewhite,
        $color-cta--t1,
        $color-nearblack,
        $color-cta--t1,
        $color-cta--t1,
        round            
    )
}

.cta--white {
    @include cta--basic(
        $color-nearblack,
        $color-purewhite,
        $color-purewhite,
        darken($color-nearblack, 6%),
        $color-purewhite,
        $color-purewhite,
        darken($color-purewhite, 6%),
        round
    )
}

///
/// Bayes program and City school colours
/// the default colour of CTA is the accent colour
///
$clp-cta-themes: (
    b: (
        $color-corporate-accent-2, // lead BG
        $color-purewhite, // lead txt
        $color-corporate-lead, // accent BG
        $color-purewhite // accent txt
    ),
    b-ug: (  
        $color-ug-accent,
        $color-nearblack,
        $color-ug-lead,
        $color-nearblack
    ),
    b-msc: (
        $color-msc-accent,
        $color-purewhite,
        $color-msc-lead,
        $color-purewhite
    ),
    b-phd: (
        $color-phd-accent,
        $color-nearblack,
        $color-phd-lead,
        $color-purewhite
    ),
    b-mba: (
        $color-nearblack,
        $color-purewhite,
        $color-mba-lead,
        $color-purewhite
    ),
    b-emba: (
        $color-emba-accent,
        $color-nearblack,
        $color-emba-lead,
        $color-purewhite,
    ),
    b-demba: (
        $color-demba-accent,
        $color-nearblack,
        $color-demba-lead,
        $color-nearblack
    ),
    b-omba: (  //online mba/msc
        $color-omba-accent,
        $color-nearblack,
        $color-omba-lead,
        $color-nearblack
    ),
    b-exec: (
        $color-exec-accent,
        $color-nearblack,
        $color-exec-lead,
        $color-purewhite
    ),
    b-cce: (
        $color-cce-accent,
        $color-nearblack,
        $color-cce-lead,
        $color-purewhite,
    ),
    c-health: (  // City school colours start
        $color-school-accent,
        $color-purewhite, 
        $color-school-health--lead,
        $color-nearblack
    ),
    c-science: (
        $color-school-accent,
        $color-purewhite,
        $color-school-science--lead,
        $color-nearblack
    ),
    c-policy: (
        $color-school-accent,
        $color-purewhite,
        $color-school-policy--lead,
        $color-purewhite
    ),
    c-creativity: (
        $color-school-accent,
        $color-purewhite,
        $color-school-creativity--lead,
        $color-purewhite
    ),
    c-law: (
        $color-school-accent,
        $color-purewhite,
        $color-school-law--lead,
        $color-purewhite
    ),
    c-pg: (
        $color-city-pg--lead,
        $color-purewhite,
        $color-city-pg--lead,
        $color-purewhite
    )
);

@each $theme, $colors in $clp-cta-themes {
    $bg-color-lead: nth($colors, 1);
    $txt-color-lead: nth($colors, 2);
    $bg-color-accent: nth($colors, 3);
    $txt-color-accent: nth($colors, 4);

    .city-lite--#{$theme} {
        .cta,
        .cta--basic {
            @if $theme == 'b-mba' {
                @include cta--basic(
                    $txt-color-lead,            // $color
                    $bg-color-lead,             // $background
                    $txt-color-lead,            // $hover-color
                    $color-black-cta-hover,     // $hover-background
                    $color-nearblack,           // $outline
                    $bg-color-lead,             // $color-border
                    $color-black-cta-hover,     // $color-border-hover
                    round                       // corners
                )
            } @else {
                @include cta--basic(
                    $txt-color-lead,            // $color
                    $bg-color-lead,             // $background
                    $txt-color-lead,            // $hover-color
                    darken($bg-color-lead, 6%), // $hover-background
                    $color-nearblack,           // $outline
                    $bg-color-lead,             // $color-border
                    darken($bg-color-lead, 6%), // $color-border-hover
                    round                       // corners
                );
            }
        }
        .cta--t2 {
            @if $theme == 'b-mba' { 
                @include cta--basic(
                    $txt-color-accent,
                    $bg-color-accent,
                    $txt-color-accent,
                    $color-black-cta-hover,
                    $color-nearblack,
                    $bg-color-accent,
                    $color-black-cta-hover,
                    round
                )
            }
            @else {
                @include cta--basic(
                    $txt-color-accent,
                    $bg-color-accent,
                    $txt-color-accent,
                    darken($bg-color-accent, 6%),
                    $color-nearblack,
                    $bg-color-accent,
                    darken($bg-color-accent, 6%),
                    round
                )
            }
        }
        .cta--outline {
            @include cta--basic(
                $txt-color-lead,
                $bg-color-lead,
                $bg-color-lead,
                $color-purewhite,
                $color-nearblack,
                $bg-color-lead,
                $bg-color-lead,
                round
            )
        }
        .cta--outline-t2 {
            @include cta--basic(
                $txt-color-accent,
                $bg-color-accent,
                $bg-color-accent,
                $color-purewhite, 
                $color-nearblack,
                $bg-color-accent,
                $bg-color-accent,
                round
            )
        } 
    }
}

.cta--corporate-b {
    @include cta--basic(
        $color-purewhite,
        $color-corporate-accent-2,
        $color-purewhite,
        darken($color-corporate-accent-2, 6%),
        $color-nearblack,
        $color-corporate-accent-2,
        darken($color-corporate-accent-2, 6%),
        round
    )
}

.cta--corporate-b-t2 {
    @include cta--basic(
        $color-purewhite,
        $color-corporate-lead,
        $color-purewhite,
        darken($color-corporate-lead, 6%),
        $color-nearblack,
        $color-corporate-lead,
        darken($color-corporate-lead, 6%),
        round
    )
}
