////
/// Footer
/// @group patterns
////
.global-footer {
    margin-left: 0;
    margin-right: 0;
    padding-left: 0;
    padding-right: 0;
        
    @include media('>=small') {
        @include standard-box();
        @include x-column-grid(24);
    }

    .global-footer__ribbon {
        background-color: $color-brand-secondary;
        padding-bottom: $measure--basic;
        padding-top: $measure--basic;        
    }
    
    .global-footer__container {
        background-color: $color-purewhite;
        @include box-shadow();
        @include default-padding();
        padding-bottom: $measure--small4;

        .global-footer__social-media__wrap {
            border-bottom: $border-width-hairline $border-style-solid $color-gray3;
            padding-bottom: $measure--basic;
            margin-bottom: $measure--basic;
            display: flex;
            flex-direction: column;

            .global-footer__social-media__heading {
                text-align: center;
                color: $color-gray9;
                @include deltatype();
                
            }

            .global-footer__social-media{
                display: flex;
                flex-direction: row;
                flex-wrap: wrap;
                list-style: none;
                justify-content: center;

                li {
                    @include gammatype();
        
                    margin-top: $measure--small2;
                    padding-left: $measure--threequarters;
                    padding-right: $measure--threequarters;
        
                    @include media('>=between') {
                        margin-right: $measure--half;
                        padding-left: 0;
                        padding-right: 0;
        
                        &:last-of-type {
                            margin-right: 0;
                        }
                    }
        
                    a {
                        @include anchor-icon(
                            $color-nearblack,
                            $color-nearblack,
                            $color-brand-primary,
                            $color-nearblack
                        );

                        border-color: $color-nearblack;
                        border-style: $border-style-solid;
                        border-width: $border-width-default;
                        display: inline-block;
                        height: $measure--small4;
                        padding: $measure--quarter;
                        text-align: center;
                        width: $measure--small4;
        
                        .icon {
                            align-items: center;
                            display: flex;
                            height: 100%;
                            justify-content: center;
                            width: 100%;
                        }

                        
                        
                    }
                }

                .global-footer__social-media__name {
                    border: 0;
                    clip: rect(0, 0, 0, 0);
                    height: 1px;
                    margin: -1px;
                    overflow: hidden;
                    padding: 0;
                    position: absolute;
                    width: 1px;
                }
            }
        }

        .global-footer__content {
            display: flex;

            @include media('<tablet') {
                flex-direction: column;
            }

            .global-footer__content__links{
                list-style: none;
                margin-top: $measure--half;
            }

            .global-footer__content__text {
                flex-basis: 80%;
                @include alphatype();

                @include media('<tablet') {
                    flex-basis: 100%;
                }

                .global-footer__content__text__summary {
                    max-width: 55ch;
                }

                .global-footer__content__text__copyright {
                    margin-top: $measure--basic;
                    font-weight: $font-weight-book;
                }
            } 

            .global-footer__content__logo{
                flex-basis: 20%;
                flex-direction: row-reverse;
                display: flex;
                align-items: center;

                @include media('<tablet') {
                    flex-basis: 100%;
                    align-self: center;
                    margin-top: $measure--small3;
                }

                a {
                    @include anchor-icon(
                        $color-nearblack,
                        $color-nearblack,
                        $color-red40,
                        $color-nearblack
                    );

                    .logo {
                        width: $measure--large1;
                        &.logo--bayes {
                            width: $measure--large4;
                        }
                    }
                }
            }
            
        }

        
    }
}

// Bayes colours
@each $theme, $colors in $clp-themes {
    $bg-color-lead: nth($colors, 9);

    .city-lite--#{$theme} {
        .global-footer__ribbon {
            background-color: $bg-color-lead;
            background: $bg-color-lead;
        }
    }
}
