.swiper-viewport {
    @include standard-grid();

    overflow: hidden;
    width: 100%;
}

.swiper-container {
    grid-column: 1/-1;
    position: relative;
    width: 100%;
    padding-bottom: $measure--medium4;

    @include media('>between') {
        width: 70%;
        grid-column: 4/-1;
    }
}

.js {
    .swiper-wrapper {
        --n: 1;
        --tx: 0px;
        display: flex;
        align-items: center;
        overflow-y: hidden;
        width: 100%;
        width: calc(var(--n) * 100%);
        // height: 50vw;
        // max-height: 100vh;
        transform: translate(
            calc(var(--tx, 0px) + var(--i, 0) / var(--n) * -100%)
        );
        align-items: baseline;
        list-style: none;
        max-width: none;
        margin-top: 0;
        margin-left: 0;

        @include media('<=between') {
            height: inherit;
            max-height: 25rem;
        }
    }
}

.swiper-slide {
    width: 100%;
    width: calc(100% / var(--n));
    user-select: none;
    pointer-events: none;

    @include media('>between') {
        margin-right: $measure--basic;
    }

    picture {
        display: flex;
        width: 100%;
    }
}

.js {
    .swiper-slide {
        opacity: 0.7;

        &.active {
            opacity: 1;
            transition: opacity 0.6s;

            .swiper-slide__text {
                opacity: 1;
            }
        }
    }

    .swiper-slide__text {
        position: relative;
        top: -$measure--small4;
        padding: $measure--small2;
        color: $color-purewhite;
        background-color: $color-blue90;
        width: calc(100% - 5rem);
        margin-right: auto;
        margin-left: auto;
        opacity: 0;
        transition: opacity 0.6s;
        @include smalltype();
        font-weight: $font-weight-book;
        line-height: $measure--small1;

        p {
            max-width: none;
            @include smalltype();
            font-weight: $font-weight-book;
            line-height: $measure--small1;
        }

        a {
            @include anchor-inline(
                $color-gray4,
                $color-gray4,
                $color-nearblack,
                $color-outline--dark
            );

            pointer-events: auto;
        }

        @include media('<=between') {
            margin-right: none;
            margin-left: $measure--basic;
            padding: $measure--basic;
        }
    }
}

.swiper-slide__image-wrapper {
    display: flex;
    justify-content: center;
    overflow: hidden;
    background-color: $color-gray2;
    @include media('<=between') {
        overflow: hidden;
        max-height: 350px;
    }

    img {
        object-fit: cover;
        width: 100%;
        @include media('<desktop') {
            max-height: 17rem;
        }
        @include media('>=desktop') {
            max-height: 25rem;
        }
    }
}

.js {
    .lazy {
        display: none;
    }
}

.no-js {
    .lazy-img {
        display: none;
    }
}

.swiper-indicator {
    position: absolute;
    font-size: $font-size--extrasmalltype;
    font-weight: $font-weight-book;
    color: $color-gray7;
    bottom: $measure--basic;
    right: $measure--huge3;

    @include media('>between') {
        bottom: $measure--medium3;
        right: $measure--basic;
    }
}

.swiper-indicator__active-slider {
    color: $color-brand-primary;
}

.swiper-indicator__slider-length {
    color: $color-gray12;
}

.swiper-buttons-wrap {
    bottom: $measure--half;
    right: $measure--basic;
    display: flex;
    position: absolute;
    z-index: z-index('above');

    @include media('>between') {
        bottom: $measure--medium1;
        right: -$measure--large4;
    }
}

.swiper-slider-arrow {
    @include control-button;
}

.swiper-button-prev {
    margin-right: $measure--basic;
}

.smooth {
    transition: transform 0.5s ease-out;
}
