@mixin anchor-discreet-before($border-hover: $color-anchor-discrete-hov) {
    &:before {
        background: $border-hover;
        bottom: -#{$border-width-default};
        content: '';
        display: block;
        height: $border-width-default;
        left: 0;
        opacity: 0;
        position: absolute;
        transition: opacity $transition-duration $transition-function,
            width $transition-duration--long $transition-function;
        width: 0;

        @include media('reducemotion') {
            transition: none;
        }
    }
}

@mixin anchor-discreet(
    $color: $color-text,
    $visited: $color-text,
    $hover: $color-text,
    $outline: $color-outline,
    $border-hover: $color-anchor-discrete-hov
) {
    @include anchor-reset();
    @include anchor-default-outline($outline);
    @include anchor-discreet-before($border-hover);

    background-image: none;
    border-bottom-color: $color;
    border-bottom-style: $border-style-solid;
    border-bottom-width: $border-width-default;
    color: $color;
    position: relative;

    &:before {
        transition: opacity $transition-duration $transition-function,
            width $transition-duration--long $transition-function;
    }

    &:focus {
        border-bottom-color: $color;
        border-bottom-style: $border-style-solid;
        border-bottom-width: $border-width-default;
        color: $color;
    }

    &:visited {
        border-bottom-color: $visited;
        border-bottom-style: $border-style-solid;
        border-bottom-width: $border-width-default;
        color: $visited;
    }

    &:hover {
        @include media('hover') {
            background-image: none;
            border-bottom-color: $hover;
            border-bottom-style: $border-style-solid;
            border-bottom-width: $border-width-default;
            color: $hover;

            &:before {
                opacity: 1;
                width: 100%;
                transition: opacity $transition-duration $transition-function,
                    width $transition-duration--long $transition-function;
            }
        }
    }

    &:active {
        border-bottom-color: $hover;
        border-bottom-style: $border-style-solid;
        border-bottom-width: $border-width-default;
    }
}

@mixin anchor-discreet--subelement(
    $color: $color-text,
    $visited: $color-text,
    $hover: $color-text,
    $outline: $color-outline,
    $border-hover: $color-anchor-discrete-hov,
    $class: 'inline-text'
) {
    @include anchor-reset();
    @include anchor-default-outline($outline);

    .#{$class} {
        @include anchor-reset();
        @include anchor-discreet-before($border-hover);

        background-image: none;
        border-bottom-color: $color;
        border-bottom-style: $border-style-solid;
        border-bottom-width: $border-width-default;
        color: $color;
        position: relative;

        &:before {
            transition: opacity $transition-duration $transition-function,
                width $transition-duration--long $transition-function;
        }
    }

    &:focus {
        .#{$class} {
            border-bottom-color: $color;
            border-bottom-style: $border-style-solid;
            border-bottom-width: $border-width-default;
            color: $color;
        }
    }

    &:visited {
        .#{$class} {
            border-bottom-color: $visited;
            border-bottom-style: $border-style-solid;
            border-bottom-width: $border-width-default;
            color: $visited;
        }
    }

    &:hover {
        @include media('hover') {
            .#{$class} {
                background-image: none;
                border-bottom-color: $hover;
                border-bottom-style: $border-style-solid;
                border-bottom-width: $border-width-default;
                color: $hover;

                &:before {
                    opacity: 1;
                    width: 100%;
                    transition: opacity $transition-duration
                            $transition-function,
                        width $transition-duration--long $transition-function;
                }
            }
        }
    }

    &:active {
        .#{$class} {
            border-bottom-color: $hover;
            border-bottom-style: $border-style-solid;
            border-bottom-width: $border-width-default;
        }
    }
}

