// Vary modal width on modal type
.dialog {
    &[data-type='video'] {
        .dialog__inner {
            max-height: calc(100% - 10vh);
            @include media('>=tablet') {
                width: 80%;
            }
            @include media('>=between') {
                width: 70%;
            }
            @include media('>=small') {
                width: 60%;
            }
        }
    }
}

.dialog-in.no-scroll {
    position: fixed;
    width: 100%;
}

.dialog {
    @include veil();
    position: fixed;
    left: 0;
    top: 0;
    bottom: 0;
    z-index: z-index('modal');
    display: block;
}

.dialog__heading {
    @include standfirst;
    @include h4style();
    font-weight: $font-weight-book;
}

.dialog__strapline {
    color: $color-brand-primary;
    @include smalltype();
}

.dialog__inner {
    display: block;
    width: 90%;
    margin-left: auto;
    margin-right: auto;
    margin-top: $measure--medium1;
    max-height: calc(100% - #{2 * $measure--small1});
    overflow-y: scroll;
    position: relative;

    @include media('>=tablet') {
        width: 65%;
        margin-top: 8vh;
        max-height: calc(100% - 30vh);
    }

    @include media('>=between') {
        width: 60%;
    }

    @include media('>=small') {
        width: 50%;
    }

    @include media('>=desktop') {
        width: 45%;
    }
}

.dialog__content {
    background: $color-purewhite;
    position: relative;
    margin-left: auto;
    margin-right: auto;
    padding: $measure--small4 $measure--medium1 $measure--medium1;

    p {
        max-width: $max-line-length;
    }

    a {
        @include anchor-inline();
    }

    .youtube-video__caption-element--text,
    .youtube-video__caption-element--time {
        font-size: $font-size--mobile;
    }
}

.dialog__close {
    color: $color-gray7;
    padding: $measure--threequarters;
    position: absolute;
    right: $border-width-default;
    top: $border-width-default;

    @include anchor-default-outline($color-outline);

    &:hover {
        @include media('hover') {
            background-image: none;
            border: none;
            background-color: $color-gray1;
            color: $color-gray10;
        }

        &:before {
            font-size: $font-size--icon + 0.2;
        }
    }

    &:active {
        background-color: $color-gray1;
        color: $color-gray10;
    }

    &:focus {
        color: $color-gray10;
    }

    &:active,
    &:focus {
        outline-offset: 0;
    }
}

.dialog__body-copy {
    margin-top: $measure--basic;
}

.dialog__underline-transition {
    @include anchor-discreet--notransition(
        $color-nearblack,
        $color-nearblack,
        $color-nearblack,
        transparent,
        $color-brand-primary
    );
}

.dialog__slider-control {
    color: $color-nearblack;
    margin-top: $measure--medium1;
    display: flex;
    max-width: $max-line-length;

    button {
        @include anchor-default-outline();
        @include font-family('default');
        html:lang(zh-Hans-CN), [lang="zh-Hans-CN"] & {
            @include font-family('han-sans');
        }
        @include body-font-features();
        @include smalltype;
        font-weight: $font-weight-book;

        min-height: $measure--medium9;
        flex: 1;
        display: flex;
        text-align: left;

        &:first-of-type {
            border-right: $border-width-default solid $color-brand-primary;
            flex-direction: row;
            padding-right: $measure--small1;

            .icon {
                margin-right: $measure--small1;
            }
        }

        &:last-of-type {
            flex-direction: row-reverse;
            padding-left: $measure--small1;

            .icon {
                margin-left: $measure--half;
            }
        }

        .dialog__underline-transition {
            display: inline;
        }

        .icon {
            align-self: center;
            display: inline-block;
        }

        @include media('<=mobile') {
            justify-content: flex-end;

            &:first-of-type {
                flex-direction: column-reverse;
                .icon {
                    align-self: flex-start;
                }
            }

            &:last-of-type {
                flex-direction: column-reverse;
                text-align: right;
                .icon {
                    align-self: flex-end;
                }
            }

            .icon {
                margin-top: $measure--half;
            }
        }
    }
}

// classnames starting with 'modal' are from Squiz, classnames starting with 'dialog' are JS generated

.modal__buttons {
    max-width: $max-line-length;

    @include media('>=tablet') {
        display: flex;
    }
}

.modal__buttons--calendar {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    a {
        @include anchor-reset-properties;
        @include anchor-default-outline($color-outline);
        border: $border-width-hairline $border-style-solid $color-gray3;
        border-bottom-color: $color-gray3;
        border-bottom-width: $border-width-hairline;
        color: $color-gray8;
        display: block;
        font-size: $font-size--alpha;
        margin-top: $measure--small4;
        padding: $measure--threequarters;
        position: relative;
        flex-basis: 48%;

        &:visited {
            border-bottom-color: $color-gray3;
            color: $color-gray8;
        }

        &:hover {
            @include media('hover') {
                @include anchor-reset-properties;
                color: $color-purewhite;
                border: $border-width-hairline $border-style-solid $color-pink90;
                border-bottom-color: $color-pink90;
                border-bottom-width: $border-width-hairline;
                background-color: $color-pink90;
                background-image: none;
                background-size: initial;
            }

            &:after {
                content: '\f271';
            }
        }

        &:focus {
            background-color: $color-gray1;
            border: $border-width-hairline $border-style-solid $color-gray4;
            border-bottom-color: $color-gray4;
            border-bottom-width: $border-width-hairline;
            color: $color-gray11;
        }

        &:active {
            background-color: $color-pink100;
            border: $border-width-hairline $border-style-solid $color-pink100;
            border-bottom-color: $color-pink100;
            border-bottom-width: $border-width-hairline;
            color: $color-purewhite;

            &:after {
                content: '\f274';
            }
        }

        &:after {
            @include font-family('icon');

            content: '\f133';
            position: absolute;
            right: $measure--threequarters;
        }
    }
}

.js .modal__body {
    display: none;
}

.modal__button {
    @include anchor-default-outline($color-outline);
    
    .inline-text {
        @include anchor-inline;
    }

    min-height: unset;
}

.no-js .modal {
    li {
        // Hide CTA on no-js
        &[data-keepformat='true'] {
            a,
            button {
                display: none;
            }
        }
    }
}
