@mixin slider-properties() {
    overflow: hidden;
    margin: $measure--basic 0;
    position: relative;

    .slide {
        top: 0;
        transition: left $transition-duration--medium ease-in-out;
        width: 100%;

        @include media('reducemotion') {
            transition: none;
        }
    }
}

.js {
    .slider {
        display: flex;
        &[data-small='true'] {
            @include media('<tablet') {
                @include slider-properties();
            }

            @include media('>=tablet') {
                display: flex;
                flex-direction: row;
                flex-wrap: wrap;
                margin: 0;
            }
        }

        &:not([data-small='true']) {
            @include media('<tablet') {
                @include slider-properties();
                margin: $measure--basic;
            }

            @include media('>=tablet') {
                @include slider-properties();

                &[data-style='buttons'] {
                    display: flex;
                    flex-direction: row;
                    flex-wrap: nowrap;
                }
            }
        }

        .slide {
            width: 100%;
            list-style: none;
            padding: $border-width-thick;
            margin-top: 0;

            &:focus {
                @include outline-properties($color-nearblack);

                outline-offset: -#{$border-width-thick};
            }

            &:last-of-type {
                margin-bottom: 0;
            }

            &.card--research {
                padding: $measure--basic;
            }
        }

