                    }
                }

                @include media('>=tablet') {
                    .slide {
                        margin-right: $measure--small2;
                        flex: 1;
                    }
                }
            }

            &:not([data-small='true']) {
                @include media('<tablet') {
                    .slide {
                        &[data-smallposition='-1'] {
                            left: -100%;
                            position: absolute;

                            &[data-smallhidden='true'] {
                                visibility: hidden;
                            }
                        }

                        &[data-smallposition='0'] {
                            left: 0;
                            position: relative;
                        }

                        &[data-smallposition='1'] {
                            left: 100%;
                            position: absolute;

                            &[data-smallhidden='true'] {
