                }

                @include media('>=tablet') {
                    .slide {
                        margin-right: $measure--small2;
                        flex: 1;

                        &[data-sliderposition='-2'],
                        &[data-sliderposition='-1'] {
                            left: -100%;
                            position: absolute;

                            &[data-hidden='true'] {
                                visibility: hidden;
                            }
                        }

                        &[data-sliderposition='0'],
                        &[data-sliderposition='1'] {
                            left: 0;
                            position: relative;
                        }

                        &[data-sliderposition='2'],
                        &[data-sliderposition='3'] {
                            left: 100%;
                            position: absolute;

                            &[data-hidden='true'] {
                                visibility: hidden;
                            }
                        }
                    }
