                            left: -100%;
                            position: absolute;

                            &[data-smallhidden='true'] {
                                visibility: hidden;
                            }
                        }

                        &[data-smallposition='0'] {
                            left: 0;
                            position: relative;
                        }

                        &[data-smallposition='1'] {
                            left: 100%;
                            position: absolute;

                            &[data-smallhidden='true'] {
                                visibility: hidden;
                            }
                        }
                    }
                }

                @include media('>=tablet') {
                    display: grid;
                    grid-template-columns: repeat(auto-fill, minmax(31%, 1fr));
                    grid-gap: $measure--small2;
                    list-style-type: none;
                    margin: $measure--basic 0;

                    .slide {
                        margin: 0 $measure--small2 0 0;
                        &:nth-of-type(3n) {
                            margin: 0;
                        }
                    }
                }
            }

            &:not([data-small='true']) {
                @include media('<tablet') {
                    .slide {
                        &[data-smallposition='-1'] {
                            left: -100%;
                            position: absolute;

                            &[data-smallhidden='true'] {
                                visibility: hidden;
                            }
                        }

                        &[data-smallposition='0'] {
                            left: 0;
                            position: relative;
                        }

                        &[data-smallposition='1'] {
                            left: 100%;
                            position: absolute;

                            &[data-smallhidden='true'] {
                                visibility: hidden;
                            }
                        }
                    }
                }

                @include media('>=tablet') {
                    .slide {
                        margin-right: $measure--small1;
                        width: calc(33% - #{$measure--small1});

                        &[data-sliderposition='-3'],
                        &[data-sliderposition='-2'],
                        &[data-sliderposition='-1'] {
                            left: -100%;
                            position: absolute;

                            &[data-hidden='true'] {
                                visibility: hidden;
                            }
                        }

                        &[data-sliderposition='0'],
                        &[data-sliderposition='1'],
                        &[data-sliderposition='2'] {
                            left: 0;
                            position: relative;
                        }

                        &[data-sliderposition='3'],
                        &[data-sliderposition='4'],
                        &[data-sliderposition='5'] {
