                        }

                        &[data-smallposition='1'] {
                            left: 100%;
                            position: absolute;

                            &[data-smallhidden='true'] {
                                visibility: hidden;
                            }
                        }
                    }
                }

                @include media('>=tablet') {
                    .slide {
                        margin-right: $measure--small2;
                        flex: 1;

                        &[data-sliderposition='-3'],
                        &[data-sliderposition='-2'],
                        &[data-sliderposition='-1'] {
                            left: -100%;
                            position: absolute;

                            &[data-hidden='true'] {
                                visibility: hidden;
                            }
                        }

                        &[data-sliderposition='0'],
                        &[data-sliderposition='1'],
                        &[data-sliderposition='2'] {
                            left: 0;
                            position: relative;
                        }

                        &[data-sliderposition='3'],
                        &[data-sliderposition='4'],
                        &[data-sliderposition='5'] {
                            left: 100%;
                            position: absolute;

                            &[data-hidden='true'] {
                                visibility: hidden;
                                
                            }
                        }
                    }
                }
            }
        }
    }

    .slider[data-style='arrows'],
    .slider[data-style='responsive'] {
        & + .slider__controls {
            align-items: center;
            display: flex;
            flex-direction: row;
            flex-wrap: nowrap;
            justify-content: flex-end;
            margin-top: $measure--basic;
            margin-bottom: $measure--basic;
            padding: $border-width-thick;

            .slider__indicator {
                @include smalltype();

                .slider__indicator__current,
                .slider__indicator__divider {
                    color: $color-gray7;
                }

                .slider__indicator__divider {
                    margin-left: $measure--quarter;
                    margin-right: $measure--quarter;

                    .slider__indicator__divider--sr {
                        @include sr-only();
                    }
                }

                .slider__indicator__total {
                    color: $color-brand-primary;
                }
            }

            .slider__controls__prev,
            .slider__controls__next {
                background-color: transparent;
                border-color: $color-brand-primary;
                border-style: $border-style-solid;
                border-width: $border-width-hairline;
                color: $color-brand-primary;
                padding: $measure--threequarters;
                transition: background-color $transition-duration ease-in,
                    border-color $transition-duration ease-in,
                    color $transition-duration ease-in;

                @include media('reducemotion') {
                    /* We can't completely disable the transition becasuse we action on transitionend */
                    transition: background-color $transition-duration--instant
                            ease-in,
                        border-color $transition-duration--instant ease-in,
                        color $transition-duration--instant ease-in;
                }

                &:focus:not(:disabled),
                &:active:not(:disabled) {
                    @include outline-properties($color-nearblack);
                }

                &:hover:not(:disabled) {
                    @include media('hover') {
                        background-color: $color-brand-primary;
                        color: $color-purewhite;
                    }
                }

                &:disabled {
                    border-color: transparent;
                    color: $color-gray7;
                    cursor: default;
                }
            }

            .slider__controls__prev,
            .slider__controls__next {
                @include font-family('icon');

                font-weight: $font-weight-normal;

                span {
                    @include sr-only();
                }
            }

            .slider__controls__prev {
                margin-left: $measure--basic;

                &:before {
                    content: fa-content($fa-var-arrow-left);
                }

                & + .slider__controls__next {
                    margin-left: $measure--basic;
                }
            }

            .slider__controls__next {
                &:before {
                    
                    content: fa-content($fa-var-arrow-right);
                }
            }
        }

        &[data-small='true'] {
            & + .slider__controls {
                @include media('>=tablet') {
                    display: none;
                }
            }
        }

        &.related-content .card.card--contact + .card--contact {
            border-top: none;
            padding-top: 0;
        }
    }

    .slider[data-style='arrows'] {
        & + .slider__controls {
            max-width: 44.4rem;
            margin-right: 0;
            margin-left: $measure--basic;
        }
    }

    .slider[data-style='responsive'] {
        margin-left: 0;
        margin-right: 0;

        // Allow space for box-shadow to display fully. On smaller screens removed to align with parent container's left/right margin/padding
        @include media('>=tablet') {
            padding: $measure--basic;
        }

        //  Edge case example to hide controls is responsive slider present with 1 slides containing 2 items
        &[data-count='1']:not([data-small='true']) {
            @include media('>=tablet') {
                & + .slider__controls {
                    display: none;
                }
            }
        }

        &[data-optimised='true'] {
            li {
                margin-top: 0;

                ul li {
                    padding: $border-width-thick;

                    a {
                        height: 100%;
                    }
                }
            }
        }

        .slide {
            .card__anchor {
                margin: 0;
            }
            ul {
                display: flex;
                max-width: 100%;
                grid-template-columns: repeat(auto-fill, minmax(31%, 1fr));
                grid-gap: $measure--small2;
                list-style-type: none;
                margin: $measure--basic 0;

                @include media('<tablet') {
                    margin: $measure--basic 0;
                }

                li {
                    flex-basis: 50%;

                    &:nth-child(2) {
                        margin-left: $measure--half;
                        margin-bottom: 0;
                    }

                    &:last-of-type {
                        margin-bottom: 0;
                    }
                }
            }
        }
    }

    // Edge case: responsive/arrow slider with 1 item where slider doesn't load
    .related-content[data-style='responsive']:not(.slider),
    .related-content[data-style='arrows']:not(.slider) {
        @include media('>=tablet') {
            justify-content: initial;
            grid-template-columns: repeat(auto-fill, minmax(31%, 1fr));
            grid-gap: $measure--small2;
            display: grid;
        }
    }

    .related-content:not(.slider) {
        margin: $measure--basic 0;
        li:only-child {
            margin-top: 0;
            margin-bottom: 0;
        }
    }

    .slider[data-style='dots']:not([data-small='true']):not([data-count='0']),
    .slider[data-style='dots'][data-small='true']:not([data-count='0']) {
        @include media('>=tablet') {
            display: flex;
            flex-wrap: wrap;

            .slide {
                &[data-sliderposition='1'],
                &[data-sliderposition='-1'],
                &[data-sliderposition='0'] {
                    visibility: visible;
                    position: static;
                }
            }
        }

        &[data-theme='dark'] {
            .slide:focus {
                outline-color: $color-purewhite;
            }
            & + .slider__controls {
                button {
                    background-color: transparent;
                    border: $border-width-hairline $border-style-solid $color-purewhite;
                    &[aria-expanded=true] {
                        background-color: $color-purewhite;
                    }
                    &:focus, &:active {
                        outline-color: $color-purewhite;
                        background-color: transparent;
                    }
                }
            }
        }

        & + .slider__controls {
            text-align: center;
            margin-bottom: $measure--basic;

            @include media('>=tablet') {
                display: none;
            }

            button {
                border-radius: 50%;
                width: $measure--small2;
                height: $measure--small2;
                background-color: $color-gray7;
                margin-right: $measure--basic;
                border: $border-width-default $border-style-dotted transparent;

                &:last-of-type {
                    margin-right: 0;
                }

                &:hover {
                    @include media('hover') {
                        background-color: $color-gray10;
                    }
                }

                &:active,
                &:focus {
                    background-color: $color-gray10;
                    border: $border-width-default $border-style-dotted
                        $color-gray10;
                    outline-color: $color-outline;
                    outline-offset: $border-width-thick;
                    outline-style: $border-style-dotted;
                    outline-width: $border-width-default;
                }

                &:focus {
                    outline-color: $color-gray7;
                }

                &[aria-expanded='true'] {
                    background-color: $color-gray10;
                    @include box-shadow-darker();

                    &:hover {
                        @include media('hover') {
                            box-shadow: none;
                        }
                    }
                    &:active,
                    &:focus {
                        box-shadow: none;
                    }
                }
            }
        }
    }
}
 