main {
    @include media('<=small') {
        > .login {
            @include default-margin();
        }
    }
}

main > .login {
    header {
        align-items: center;
        display: flex;
        flex-direction: column;
        flex-wrap: nowrap;
        justify-content: center;
        padding-top: $measure--small2;

        a {
            @include anchor-img();

            display: inline-block;
            width: 100%;

            @include media('<small') {
                height: $measure--medium7;
            }

            @include media('>=small') {
                max-width: $measure--huge5;
            }

            .logo {
                width: 100%;

                @include media('<small') {
                    height: 100%;
                }
            }
        }

        h1 {
            @include sr-only();

            margin-top: $heading-margin;
        }
    }

    .login__message {
        max-width: unset;
        text-align: center;
    }

    form {
        @include default-padding();

        background-color: $color-gray1;
        margin-top: $measure--basic;
        padding-bottom: $measure--small2;
        padding-top: $measure--basic;

        input[type='submit'] {
            display: block;
            margin-left: auto;
            margin-right: auto;
        }
    }
    
    footer {
        margin-top: $measure--small2;
        padding-bottom: $measure--small2;

        p {
            max-width: unset;
            text-align: center;
        }
    }
}
