////
/// Outline mixins
/// @group abstracts
////
@mixin outline-properties($color: $color-outline) {
    outline-color: $color;
    outline-offset: $border-width-thick;
    outline-style: $border-style-dotted;
    outline-width: $border-width-default;
}

@mixin anchor-default-outline($color: $color-outline) {
    &:focus,
    &:active {
        @include outline-properties($color);
    }
}
