////
/// Accordion
/// @group patterns
////

@mixin accordion() {
    border-bottom: $border-width-hairline $border-style-solid $color-gray2;
    margin-top: 0;

    // Heading focus/active needed for Firefox and Safari. Other interaction states on button
    &:focus {
        outline: $border-width-default $border-style-dotted $color-gray11;
        border: 0;
    }
    &:active {
        border: 0;
        border-bottom: $border-width-hairline $border-style-solid transparent;
        outline: 0;
    }

    button {
        @include betatype();
        width: 100%;
        text-align: left;
        padding: $measure--basic;
        color: $color-linkblue;
        outline: $border-width-default $border-style-solid transparent;

        div {
            @include ninetyten-grid();
            grid-column-gap: 0;
        }

        .accordion__heading__indicator {
            align-self: center;
            text-align: right;
            height: 100%;

            &:before {
                content: fa-content($fa-var-plus);
                display: inline-block;
                text-decoration: none;
            }
        }

        &:hover {
            @include media('hover') {
                text-decoration: underline;
                color: $color-linkblue;
                outline: $border-width-default $border-style-solid transparent;
                background: $color-gray1;
            }
        }
        &:focus {
            outline: $border-width-default $border-style-dotted $color-gray11;
            border: 0;
        }
        &:active {
            outline: $border-width-default $border-style-dotted $color-gray11;
        }
    }

    // Set top margin to give space to preceding content and stop 'jump' effect when first button is clicked
    &:first-of-type {
        margin-top: $measure--basic;
    }

    &[data-open='true'] {
        margin-top: $measure--basic;
        border-bottom: 0;

        &:focus,
        &:active {
            outline: 0;
        }

        button {
            border-top: $border-width-default $border-style-dotted $color-gray11;
            border-left: $border-width-default $border-style-dotted
                $color-gray11;
            border-right: $border-width-default $border-style-dotted
                $color-gray11;
            border-bottom: $border-width-hairline $border-style-solid
                $color-gray3;
            color: $color-gray11;
            padding: 0;

            .accordion__heading__text,
            .accordion__heading__indicator {
                padding: $measure--basic;
            }

            .accordion__heading__text {
                &:hover {
                    @include media('hover') {
                        text-decoration: underline;
                    }
                }
            }

            .accordion__heading__indicator {
                &:before {
                    content: fa-content($fa-var-minus);
                    display: inline-block;
                    text-decoration: none;
                }
            }

            &:focus,
            &:active {
                border-top: $border-width-default $border-style-dotted
                    $color-gray11;
                border-left: $border-width-default $border-style-dotted
                    $color-gray11;
                border-right: $border-width-default $border-style-dotted
                    $color-gray11;
                border-bottom: $border-width-hairline $border-style-solid
                    $color-gray3;
                outline: 0;
            }

            &:hover {
                @include media('hover') {
                    background: $color-gray1;
                    color: $color-gray11;
                }
            }
        }

        & + .accordion__body {
            border-top: 0;
            border-left: $border-width-default $border-style-dotted
                $color-gray11;
            border-right: $border-width-default $border-style-dotted
                $color-gray11;
            border-bottom: $border-width-default $border-style-dotted
                $color-gray11;

            p {
                margin: $measure--basic 0;
            }

            &:focus,
            &:active {
                /// body with border on focus only
                border-style: $border-style-dotted;
                border-color: $color-gray11;
            }

            & > :first-child {
                margin-top: 0;
            }

            & > :last-child {
                margin-bottom: 0;
            }
        }

        & + .accordion__body:first-child {
            margin-top: 0;
        }
    }
}

.js .accordion {
    
    @include media('screen') {
        & > .accordion__body {
            background-color: $color-gray1;
            padding: $measure--basic;
        }

        & > .accordion__heading {
            @include accordion();

            &[data-open='true'] {
                button {
                    background: $color-gray1;
                }

                & + .accordion__body {
                    margin-bottom: $measure--basic;
                }
            }

            & + .accordion__body {
                overflow-y: hidden;
                transition-duration: $transition-duration;
                transition-property: height;
                transition-timing-function: $transition-function;

                &[data-closed='true'] {
                    display: none;
                }
            }
        }
    }
}

// Connect camplaign specific styles
.accordion__wrapper {
    grid-column: 1/-1;
    background: $color-gray1;
    display: grid;
    @include standard-grid();
    padding-top: $measure--small3;
    padding-bottom: $measure--small4;
    margin-top: $measure--small2;

    .accordion__main-heading {
        grid-column: 1/-1;
        text-align: center;
        @include default-margin();
        margin: 0 0 $measure--small1 0;
        
        @include media('>=small') {
            grid-column: 7/-7;
        }
    }

    .accordion__description {
        grid-column: 1/-1;
        margin:0 $measure--small1 $measure--small1 $measure--small1;
        text-align: center;
        display: flex;
        justify-content: center;
        p {
            margin: 0;
        }
        @include media('>=small') {
            grid-column: 7/-7;
        }
    }

    .accordion {
        grid-column: 1/-1;
        @include default-margin();
        
        @include media('>=small') {
            margin: 0;
            grid-column: 7/-7;
        }

        .accordion__heading {
            background: $color-purewhite;
            
            &:first-of-type {
                margin-top: 0;
            }

            &[data-open="true"]{
                button {
                    background-color: $color-accordion-theme;
                    color: $color-purewhite;

                    &:hover {
                        background-color: $color-accordion-theme;
                        color: $color-purewhite;
                    }
                }
            }

            button {
                color: $color-nearblack;
                padding: 0;
                display: block;

                .accordion__heading__text {
                    padding: $measure--basic;
                }
                .accordion__heading__indicator {
                    background-color: $color-accordion-theme;
                    color: $color-purewhite;
                    align-self: normal;
                    padding: 1rem;
                    text-align: center;
                    
                    &:before {
                        vertical-align: sub;
                    }
                }

                &:hover {
                    color: $color-nearblack;
                    background: inherit;
                }
            }
        }

        .accordion__body {
            background: $color-purewhite;
        }
    }

    #accordion {
        grid-column: 1/-1;
        @include default-margin();
        
        @include media('>=small') {
            margin: 0;
            grid-column: 7/-7;
        }
    }
}

///
/// bayes colours
///
@each $theme, $colors in $clp-themes {
    $bg-color-lead: nth($colors, 1);
    $txt-color-lead: nth($colors, 2);

    .city-lite--#{$theme} {
        .accordion__wrapper {
            .accordion {
                .accordion__heading {    
                    &[data-open="true"]{
                        button {
                            background-color: $bg-color-lead;
                            color: $txt-color-lead;

                            &:hover {
                                background-color: $bg-color-lead;
                                color: $txt-color-lead;
                            }
                        }
                    }

                    .accordion__heading__indicator {
                        background-color: $bg-color-lead;
                        color: $txt-color-lead;
                    }
                }
            }
        }
    }
}
