@mixin anchor-hover-background-settings($color: $color-inline-link) {
    transition: background-size $transition-duration $transition-function;
    background-repeat: no-repeat;
    background-size: 100% 0;
    background-position: 0 100%;
    background-image: linear-gradient(120deg, $color 0%, $color 100%);

    @include media('reducemotion') {
        transition: none;
    }

    &:hover {
        @include media('hover') {
            background-size: 100% 100%;
            background-image: linear-gradient(120deg, $color 0%, $color 100%);
        }
    }
}

@mixin anchor-hover-background($color: $color-inline-link) {
    @include anchor-hover-background-settings($color);
}
