form:not([class]),
.form {
    fieldset {
        border: none;
        margin: 0;
        padding: 0;

        .form__label,
        label {
            @include content-style();

            display: block;
            margin-bottom: $measure--quarter;
            margin-top: $measure--small2;
        }

        .form__label--required {
            &:after {
                content: ' *';
                color: $color-required;
            }
        }
    }

    input,
    textarea {
        @include body-baseline();

        &:focus {
            color: $color-gray10;
            font-style: normal;
            font-weight: $font-weight-medium;
            outline: 0;
        }

        &::placeholder {
            font-style: italic;
        }

        &:placeholder-shown {
            color: $color-gray7;
        }

        &:not([type='submit']) {
            width: 100%;

            &:focus {
                @include outline-properties();
            }
        }

        &[type='submit'] {
            @include cta--basic(
                $color-purewhite,
                $color-required,
                $color-purewhite,
                $color-required--hover,
                $color-purewhite,
                $color-required,
                $color-required--hover);

            -webkit-appearance: none;
            appearance: none;
            border: 0;
            margin-top: $measure--small2;

            &:active,
            &:focus {
                @include outline-properties();
            }
        }
    }

    .form__submit-container {
        text-align: right;
    }

    .form__required-note--marker {
        color: $color-required;
    }
}

/* FormAssembly */
.city-lite .wFormContainer {
    margin-bottom: 0;
    margin-left: 0;
    margin-right: 0;
    margin-top: 0;
    max-width: unset;

    .wForm {
        padding-left: 0;
        padding-right: 0;
    }
}
