////
/// Pathway cards block
/// @group patterns
////
.pathway {

    .pathway__heading {
        margin-top: 0;
        padding-left: $measure--half;
        padding-right: $measure--half;
    }

    .pathway__cards {
        @include media('>=tablet') {
            display: grid;
            grid-template-columns: repeat(3, 1fr);
            grid-column-gap: $measure--small2;
        }

        list-style: none;
        margin: 0;
        max-width: unset;
        padding-bottom: $measure--small1;

        .card--pathway {
            margin-top: $measure--basic;

            a {
                @include pathway-card();
                &.card--pathway__content--border {
                    border-top: $measure--half solid $color-pathwaycard-border;
                }
            }

            button {
                @include pathway-card();
                &.card--pathway__content--border {
                    border-top: $measure--half solid $color-pathwaycard-border;
                }

                .card--pathway__content__img{
                    position: relative;
                    .card--pathway__content__yt-play-icon{
                        position: absolute;
                        top: 50%;
                        left: 50%;
                        transform: translateY(-50%) translateX(-50%);
                        width: $font-size--pathway-yt-icon;
                    }
                }

                .card--pathway__content__text{
                    text-align: left;
                }
            }

            .modal{
                list-style: none;
                margin-left: initial;
                margin-top: initial;
                max-width: initial;

                li{
                    margin-top:initial;
                }
            }

            .card--pathway__content {
                overflow: hidden;
                
                @include media('>=mobile', '<tablet') {
                    display: flex;
                    picture {
                        max-width: $measure--huge5;
                    }
                }
            }

            .card--pathway__content__text {
                padding: 0 $measure--basic $measure--basic $measure--basic;
            }

            .card--pathway__content__img {
                img {
                    display: block;
                }
            }


            .card--pathway__content__heading {
                @include betatype();
                .icon {
                    margin-left: $measure--quarter;
                }
            }

        }
    }
}

///
/// Bayes colours
///
@each $theme, $colors in $clp-themes {
    $bg-color-lead: nth($colors, 1);

    .city-lite--#{$theme} {
        .pathway {
            .pathway__cards {
                .card--pathway {
                    a {
                        &.card--pathway__content--border {
                            border-top: $measure--half $border-style-solid $bg-color-lead;
                        }
                    }
                }
            }
        }
    }
}